/* src/components/student/views/LessonPlayerA2/lesson-audio-player.css */
/* 🎵 ELEGANT AUDIO PLAYER - FIXED LIGHT MODE - Clean, Round, Perfect */

.elegant-audio-player {
  margin: 1.5rem 0;
  font-family: -apple-system, BlinkMacSystemFont, "SF Pro Display", "Segoe UI", Roboto, sans-serif;
}

.player-container {
  background: var(--bg-primary, rgba(255, 255, 255, 0.9));
  backdrop-filter: blur(20px);
  border: 1px solid var(--border-primary, rgba(148, 163, 184, 0.3));
  border-radius: 24px;
  padding: 1.5rem;
  box-shadow: var(--shadow-md, 0 8px 32px rgba(0, 0, 0, 0.15));
  position: relative;
  text-align: center;
  transition: all 0.3s ease;
}

.player-container:hover {
  background: var(--bg-secondary, rgba(255, 255, 255, 0.95));
  box-shadow: var(--shadow-lg, 0 16px 48px rgba(0, 0, 0, 0.2));
}

/* Elegant accent stripe */
.player-container::before {
  content: '';
  position: absolute;
  top: 0;
  left: 16px;
  right: 16px;
  height: 3px;
  background: var(--theme-color, #3b82f6);
  border-radius: 0 0 12px 12px;
  box-shadow: 0 2px 8px rgba(var(--theme-color, 59, 130, 246), 0.3);
}

/* === LESSON TITLE === */
.lesson-info {
  margin-bottom: 1.25rem;
}

.lesson-info h4 {
  font-size: 1.25rem;
  font-weight: 600;
  color: var(--text-primary, #1e293b);
  margin: 0;
  letter-spacing: -0.01em;
}

/* === TIME DISPLAY === */
.time-display {
  font-family: 'SF Mono', Monaco, monospace;
  font-size: 1.75rem;
  font-weight: 700;
  margin: 1.25rem 0;
  color: var(--text-primary, #1e293b);
  background: var(--bg-secondary, rgba(255, 255, 255, 0.95));
  backdrop-filter: blur(10px);
  padding: 0.75rem 1.25rem;
  border-radius: 16px;
  display: inline-block;
  border: 1px solid var(--border-primary, rgba(148, 163, 184, 0.3));
  box-shadow: var(--shadow-sm, 0 4px 16px rgba(0, 0, 0, 0.1));
}

.current-time {
  color: var(--theme-color, #3b82f6);
}

.separator {
  color: var(--text-muted, #64748b);
  margin: 0 0.5rem;
}

.total-time {
  color: var(--text-secondary, #475569);
}

/* === PROGRESS BAR === */
.progress-section {
  margin: 1.5rem 0;
}

.progress-bar {
  width: 100%;
  height: 8px;
  background: var(--bg-tertiary, rgba(248, 250, 252, 0.8));
  border-radius: 12px;
  cursor: pointer;
  position: relative;
  overflow: hidden;
  transition: all 0.2s ease;
  border: 1px solid var(--border-secondary, rgba(148, 163, 184, 0.2));
}

.progress-bar:hover {
  background: var(--bg-secondary, rgba(255, 255, 255, 0.95));
  box-shadow: var(--shadow-sm, 0 4px 16px rgba(0, 0, 0, 0.1));
}

.progress-fill {
  height: 100%;
  background: linear-gradient(90deg, var(--theme-color, #3b82f6), color-mix(in srgb, var(--theme-color, #3b82f6) 80%, white));
  border-radius: 12px;
  transition: width 0.3s cubic-bezier(0.25, 0.46, 0.45, 0.94);
  box-shadow: 0 0 12px rgba(var(--theme-color, 59, 130, 246), 0.4);
}

.progress-thumb {
  position: absolute;
  top: 50%;
  width: 16px;
  height: 16px;
  background: white;
  border: 2px solid var(--theme-color, #3b82f6);
  border-radius: 50%;
  transform: translateY(-50%) translateX(-50%);
  cursor: pointer;
  box-shadow: var(--shadow-sm, 0 4px 16px rgba(0, 0, 0, 0.1));
  transition: all 0.2s ease;
  opacity: 0;
}

.progress-bar:hover .progress-thumb {
  opacity: 1;
}

.progress-thumb:hover {
  box-shadow: var(--shadow-md, 0 8px 32px rgba(0, 0, 0, 0.15));
  transform: translateY(-50%) translateX(-50%) scale(1.1);
}

/* === MAIN CONTROLS === */
.main-controls {
  display: flex;
  justify-content: center;
  align-items: flex-end;
  gap: 2rem;
  margin: 2rem 0;
}

.control-group {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 0.5rem;
}

.control-btn {
  border: none;
  border-radius: 50%;
  cursor: pointer;
  transition: all 0.3s cubic-bezier(0.25, 0.46, 0.45, 0.94);
  display: flex;
  align-items: center;
  justify-content: center;
  font-family: inherit;
  backdrop-filter: blur(10px);
}

.skip-btn {
  width: 56px;
  height: 56px;
  background: var(--bg-secondary, rgba(255, 255, 255, 0.95));
  color: var(--text-primary, #1e293b);
  border: 2px solid var(--border-primary, rgba(148, 163, 184, 0.3));
  box-shadow: var(--shadow-sm, 0 4px 16px rgba(0, 0, 0, 0.1));
}

.skip-btn:hover:not(:disabled) {
  background: var(--theme-color, #3b82f6);
  color: white;
  box-shadow: 0 6px 20px rgba(var(--theme-color, 59, 130, 246), 0.25);
  border-color: var(--theme-color, #3b82f6);
  transform: translateY(-2px);
}

.play-btn {
  width: 80px;
  height: 80px;
  background: linear-gradient(145deg, var(--theme-color, #3b82f6), color-mix(in srgb, var(--theme-color, #3b82f6) 80%, black));
  color: white;
  box-shadow: 0 8px 32px rgba(var(--theme-color, 59, 130, 246), 0.4);
  border: 3px solid rgba(255, 255, 255, 0.2);
}

.play-btn:hover:not(:disabled) {
  box-shadow: 0 10px 36px rgba(var(--theme-color, 59, 130, 246), 0.4);
  transform: translateY(-2px);
}

.play-btn:active {
  transform: scale(0.98);
}

.control-btn:disabled {
  opacity: 0.4;
  cursor: not-allowed;
  transform: none !important;
}

/* Skip labels - Close to buttons */
.skip-label {
  font-size: 0.7rem;
  color: var(--text-muted, #64748b);
  font-weight: 600;
  background: var(--bg-tertiary, rgba(248, 250, 252, 0.8));
  backdrop-filter: blur(10px);
  padding: 0.25rem 0.5rem;
  border-radius: 8px;
  border: 1px solid var(--border-secondary, rgba(148, 163, 184, 0.2));
  margin-top: 0.5rem;
}

/* === SPEED CONTROLS === */
.speed-section {
  margin-top: 1.5rem;
}

.speed-buttons {
  display: flex;
  justify-content: center;
  gap: 0.5rem;
  background: var(--bg-secondary, rgba(255, 255, 255, 0.95));
  backdrop-filter: blur(15px);
  padding: 0.5rem;
  border-radius: 16px;
  border: 1px solid var(--border-primary, rgba(148, 163, 184, 0.3));
  box-shadow: var(--shadow-sm, 0 4px 16px rgba(0, 0, 0, 0.1));
}

.speed-btn {
  padding: 0.625rem 1rem;
  border: 2px solid transparent;
  background: transparent;
  color: var(--text-secondary, #475569);
  border-radius: 12px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s cubic-bezier(0.25, 0.46, 0.45, 0.94);
  min-width: 52px;
  font-size: 0.85rem;
  position: relative;
  overflow: hidden;
}

.speed-btn::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: linear-gradient(145deg, var(--theme-color, #3b82f6), color-mix(in srgb, var(--theme-color, #3b82f6) 80%, black));
  opacity: 0;
  transition: opacity 0.3s ease;
  border-radius: 10px;
}

.speed-btn span {
  position: relative;
  z-index: 1;
}

.speed-btn:hover {
  color: var(--text-primary, #1e293b);
  background: var(--bg-tertiary, rgba(248, 250, 252, 0.8));
  box-shadow: var(--shadow-sm, 0 4px 16px rgba(0, 0, 0, 0.1));
}

.speed-btn.active {
  color: white;
  background: var(--theme-color, #3b82f6);
  box-shadow: 0 4px 16px rgba(var(--theme-color, 59, 130, 246), 0.4);
}

.speed-btn.active::before {
  opacity: 1;
}

/* === MOBILE RESPONSIVE === */
@media (max-width: 768px) {
  .player-container {
    padding: 1.25rem;
    border-radius: 20px;
  }
  
  .lesson-info h4 {
    font-size: 1.125rem;
  }
  
  .time-display {
    font-size: 1.5rem;
    padding: 0.625rem 1rem;
    border-radius: 14px;
  }
  
  .main-controls {
    gap: 1.5rem;
  }
  
  .skip-btn {
    width: 48px;
    height: 48px;
  }
  
  .play-btn {
    width: 68px;
    height: 68px;
  }
  
  .speed-buttons {
    flex-wrap: wrap;
    gap: 0.375rem;
    padding: 0.375rem;
    border-radius: 14px;
  }
  
  .speed-btn {
    padding: 0.5rem 0.875rem;
    min-width: 44px;
    font-size: 0.8rem;
    border-radius: 10px;
  }
  
  .skip-label {
    font-size: 0.65rem;
    padding: 0.2rem 0.4rem;
  }
}

/* === SERIES THEME VARIANTS === */
.elegant-audio-player[style*="--theme-color: #0ea5e9"] .progress-fill {
  background: linear-gradient(90deg, #0ea5e9, #0284c7);
  box-shadow: 0 0 12px rgba(14, 165, 233, 0.4);
}

.elegant-audio-player[style*="--theme-color: #a855f7"] .progress-fill {
  background: linear-gradient(90deg, #a855f7, #9333ea);
  box-shadow: 0 0 12px rgba(168, 85, 247, 0.4);
}

.elegant-audio-player[style*="--theme-color: #ec4899"] .progress-fill {
  background: linear-gradient(90deg, #ec4899, #db2777);
  box-shadow: 0 0 12px rgba(236, 72, 153, 0.4);
}

/* === DARK MODE SPECIFIC OVERRIDES === */
.dark .player-container {
  background: rgba(255, 255, 255, 0.08);
  border: 1px solid rgba(255, 255, 255, 0.15);
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.3);
}

.dark .player-container:hover {
  background: rgba(255, 255, 255, 0.10);
}

.dark .lesson-info h4 {
  color: #ffffff;
}

.dark .time-display {
  background: rgba(255, 255, 255, 0.08);
  border: 1px solid rgba(255, 255, 255, 0.15);
  color: #ffffff;
}

.dark .total-time {
  color: rgba(255, 255, 255, 0.8);
}

.dark .separator {
  color: rgba(255, 255, 255, 0.6);
}

.dark .progress-bar {
  background: rgba(255, 255, 255, 0.1);
  border: 1px solid rgba(255, 255, 255, 0.15);
}

.dark .progress-bar:hover {
  background: rgba(255, 255, 255, 0.15);
}

.dark .skip-btn {
  background: rgba(255, 255, 255, 0.1);
  color: #ffffff;
  border: 2px solid rgba(255, 255, 255, 0.2);
}

.dark .speed-buttons {
  background: rgba(255, 255, 255, 0.08);
  border: 1px solid rgba(255, 255, 255, 0.15);
}

.dark .speed-btn {
  color: rgba(255, 255, 255, 0.8);
}

.dark .speed-btn:hover {
  color: #ffffff;
  background: rgba(255, 255, 255, 0.08);
}

.dark .skip-label {
  color: rgba(255, 255, 255, 0.6);
  background: rgba(255, 255, 255, 0.08);
  border: 1px solid rgba(255, 255, 255, 0.15);
}

/* === LOADING STATE === */
.player-container.loading {
  opacity: 0.7;
  pointer-events: none;
}

.player-container.loading::after {
  content: '';
  position: absolute;
  top: 50%;
  left: 50%;
  width: 20px;
  height: 20px;
  border: 2px solid transparent;
  border-top: 2px solid var(--theme-color, #3b82f6);
  border-radius: 50%;
  animation: spin 1s linear infinite;
  transform: translate(-50%, -50%);
}

@keyframes spin {
  0% { transform: translate(-50%, -50%) rotate(0deg); }
  100% { transform: translate(-50%, -50%) rotate(360deg); }
}