/* src/components/TeacherDashboard.css */
/* TEACHER DASHBOARD LAYOUT - Works with JavaScript theme system */

/* 📱 Main Layout */
.dashboard-container {
  min-height: 100vh;
}

.dashboard-header {
  border-bottom: 1px solid #e5e7eb;
}

.header-content {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 1rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 4rem;
}

@media (min-width: 640px) {
  .header-content {
    padding: 0 1.5rem;
  }
}

@media (min-width: 1024px) {
  .header-content {
    padding: 0 2rem;
  }
}

.dashboard-title {
  font-size: 1.25rem;
  font-weight: 700;
  margin: 0;
}

.user-info {
  display: flex;
  align-items: center;
  gap: 1rem;
}

.welcome-text {
  font-size: 0.875rem;
}

/* 🏠 Main Content Layout */
.dashboard-main {
  max-width: 1200px;
  margin: 0 auto;
  padding: 2rem 1rem;
}

@media (min-width: 640px) {
  .dashboard-main {
    padding: 2rem 1.5rem;
  }
}

@media (min-width: 1024px) {
  .dashboard-main {
    padding: 2rem;
  }
}

/* 📋 Tab Navigation Layout */
.tab-navigation {
  margin-bottom: 2rem;
}

.tab-nav-list {
  display: flex;
  gap: 2rem;
}

.tab-button {
  padding: 0.5rem 0.25rem;
  border-bottom: 2px solid transparent;
  font-weight: 500;
  font-size: 0.875rem;
  transition: all 0.2s ease;
  background: none;
  border-top: none;
  border-left: none;
  border-right: none;
  cursor: pointer;
}

/* 💬 Message Display Layout */
.message-alert {
  margin-bottom: 1.5rem;
  padding: 1rem;
  border-radius: 8px;
  border: 1px solid;
}

/* 📊 Overview Section Layout */
.overview-section {
  border-radius: 12px;
  padding: 1.5rem;
}

.overview-title {
  font-size: 1.5rem;
  font-weight: 700;
  margin: 0 0 1rem;
}

.stats-grid {
  display: grid;
  grid-template-columns: 1fr;
  gap: 1rem;
  margin-bottom: 1.5rem;
}

@media (min-width: 768px) {
  .stats-grid {
    grid-template-columns: repeat(2, 1fr);
  }
}

@media (min-width: 1024px) {
  .stats-grid {
    grid-template-columns: repeat(4, 1fr);
  }
}

.stat-number {
  font-size: 1.5rem;
  font-weight: 700;
}

.stat-label {
  font-size: 0.875rem;
  margin-top: 0.25rem;
}

.quick-actions {
  border-top: 1px solid #e5e7eb;
  padding-top: 1rem;
}

.actions-title {
  font-weight: 600;
  margin: 0 0 0.5rem;
}

.actions-buttons {
  display: flex;
  flex-wrap: wrap;
  gap: 0.5rem;
}

/* 👥 Students Section Layout */
.students-section {
  border-radius: 12px;
}

.students-header {
  padding: 1rem 1.5rem;
  border-bottom: 1px solid #e5e7eb;
}

.students-title {
  font-size: 1.25rem;
  font-weight: 700;
  margin: 0;
}

.empty-state {
  padding: 2rem;
  text-align: center;
}

.empty-icon {
  font-size: 2.5rem;
  margin-bottom: 1rem;
}

.empty-title {
  font-size: 1.125rem;
  font-weight: 500;
  margin: 0 0 0.5rem;
}

.empty-description {
  margin: 0 0 1rem;
}

.table-container {
  overflow-x: auto;
}

.students-table {
  width: 100%;
  border-collapse: separate;
  border-spacing: 0;
}

.table-head {
  background-color: #f9fafb;
}

.table-header-cell {
  padding: 0.75rem 1.5rem;
  text-align: left;
  font-size: 0.75rem;
  font-weight: 500;
  text-transform: uppercase;
  letter-spacing: 0.05em;
}

.table-body {
  background: white;
}

.table-row {
  border-top: 1px solid #e5e7eb;
  transition: background-color 0.2s ease;
}

.table-row:hover {
  background-color: #f9fafb;
}

.table-cell {
  padding: 1rem 1.5rem;
  white-space: nowrap;
}

.student-info {
  display: flex;
  align-items: center;
}

.student-name {
  font-size: 0.875rem;
  font-weight: 500;
}

.student-username {
  font-size: 0.875rem;
  margin-left: 0.5rem;
}

.series-badge {
  display: inline-flex;
  align-items: center;
  padding: 0.125rem 0.625rem;
  border-radius: 9999px;
  font-size: 0.75rem;
  font-weight: 500;
}

.progress-text {
  font-size: 0.875rem;
}

.last-active {
  font-size: 0.875rem;
}

/* ➕ Create Student Section Layout */
.create-section {
  border-radius: 12px;
  padding: 1.5rem;
}

.create-title {
  font-size: 1.25rem;
  font-weight: 700;
  margin: 0 0 1.5rem;
}

.create-form {
  max-width: 28rem;
}

.form-group {
  margin-bottom: 1rem;
}

.form-select {
  width: 100%;
  padding: 0.75rem 1rem;
  border: 1px solid #d1d5db;
  border-radius: 8px;
  font-size: 1rem;
  transition: all 0.2s ease;
  background: white;
}

.form-help {
  font-size: 0.75rem;
  margin-top: 0.25rem;
}

.submit-button {
  width: 100%;
  padding: 0.75rem 1rem;
  border-radius: 8px;
  border: none;
  font-weight: 500;
  cursor: pointer;
  transition: all 0.2s ease;
  display: flex;
  align-items: center;
  justify-content: center;
  min-height: 48px;
}

.submit-button:disabled {
  opacity: 0.5;
  cursor: not-allowed;
}

.submit-spinner {
  width: 20px;
  height: 20px;
  border: 2px solid rgba(255, 255, 255, 0.3);
  border-top: 2px solid white;
  border-radius: 50%;
  animation: spin 1s linear infinite;
  margin-right: 0.5rem;
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}