/* src/components/student/StudentPortal/portal.css */
/* 🎯 APPLE-STYLE STUDENTPORTAL - No more inline CSS chaos! */

/* ===================================================================== */
/* CSS RESET & BASE STYLES - PREVENTS CASCADE CONFLICTS */
/* ===================================================================== */

/* Container - Apple-style full viewport */
.studentportal-container {
  min-height: 100vh;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, "Helvetica Neue", Arial, sans-serif;
  background: 
    /* 🎨 LV PATTERN - LIGHT MODE (subtle) */
    repeating-linear-gradient(
      45deg,
      transparent,
      transparent 25px,
      rgba(0, 0, 0, 0.02) 25px,
      rgba(0, 0, 0, 0.02) 50px
    ),
    repeating-linear-gradient(
      -45deg,
      transparent,
      transparent 25px,
      rgba(0, 0, 0, 0.015) 25px,
      rgba(0, 0, 0, 0.015) 50px
    ),
    var(--bg-gradient);
  color: var(--text-primary);
  position: relative;
  overflow-x: hidden;
}

/* 🌙 DARK MODE LV PATTERN */
.dark .studentportal-container {
  background: 
    /* 🎨 LV PATTERN - DARK MODE (subtle) */
    repeating-linear-gradient(
      45deg,
      transparent,
      transparent 25px,
      rgba(255, 255, 255, 0.02) 25px,
      rgba(255, 255, 255, 0.02) 50px
    ),
    repeating-linear-gradient(
      -45deg,
      transparent,
      transparent 25px,
      rgba(255, 255, 255, 0.015) 25px,
      rgba(255, 255, 255, 0.015) 50px
    ),
    var(--bg-gradient);
}

/* Loading States - Clean and minimal */
.portal-loading-container {
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  background: var(--bg-gradient);
}

.portal-loading-content {
  text-align: center;
}

.portal-loading-spinner {
  width: 48px;
  height: 48px;
  border: 3px solid var(--border-primary);
  border-top: 3px solid var(--brand-blue);
  border-radius: 50%;
  animation: portal-spin 1s linear infinite;
  margin: 0 auto 1rem;
}

.portal-loading-text {
  font-size: 1.125rem;
  font-weight: 500;
  color: var(--text-secondary);
  letter-spacing: -0.01em;
}

/* ===================================================================== */
/* HEADER SECTION - APPLE-STYLE CLEAN DESIGN */
/* ===================================================================== */

.portal-header {
  background: linear-gradient(135deg, #1e40af 0%, #1d4ed8 100%);
  border-radius: 0 0 24px 24px;
  position: relative;
  overflow: hidden;
  padding: 1.5rem 1rem 2rem;
  box-shadow: 0 4px 16px rgba(0, 0, 0, 0.15);
  z-index: 100; /* Prevent z-index conflicts */
}

/* Background Pattern - Subtle Apple-style texture */
.portal-header-pattern {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: 
    radial-gradient(circle at center, rgba(255, 255, 255, 0.15) 2px, transparent 2px);
  background-size: 25px 25px;
  opacity: 0.5;
  pointer-events: none;
}

/* Dark Mode Toggle - Fixed positioning */
.portal-header-toggle {
  position: absolute;
  top: 1.5rem;
  right: 1.5rem;
  z-index: 1000;
}

/* Header Content Container - Centered with max-width */
.portal-header-content {
  position: relative;
  max-width: 1200px;
  margin: 0 auto;
  text-align: center;
}

/* Back Button - Apple-style subtle interaction */
.portal-back-button {
  position: absolute;
  top: 0;
  left: 0;
  display: flex;
  align-items: center;
  gap: 0.5rem;
  color: rgba(255, 255, 255, 0.9);
  background: none;
  border: none;
  cursor: pointer;
  font-size: 0.95rem;
  font-weight: 400;
  transition: all 0.2s ease;
  padding: 0.5rem;
  border-radius: 8px;
  font-family: inherit;
}

.portal-back-button:hover {
  color: white;
  transform: translateX(-2px);
  background: rgba(255, 255, 255, 0.1);
}

.portal-back-button:active {
  transform: translateX(-1px);
  background: rgba(255, 255, 255, 0.15);
}

/* ===================================================================== */
/* BRANDING SECTION - ACADEMY IDENTITY */
/* ===================================================================== */

.portal-branding-section {
  margin-bottom: 1.5rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 0.75rem;
}

.portal-academy-badge {
  display: inline-block;
  background: #dc2626;
  color: white;
  padding: 0.75rem 1.5rem;
  border-radius: 16px;
  font-size: 1.25rem;
  font-weight: 600;
  margin-bottom: 0.75rem;
  border: 1px solid rgba(255, 255, 255, 0.1);
  letter-spacing: -0.01em;
  box-shadow: 0 4px 12px rgba(220, 38, 38, 0.4);
}

/* ===================================================================== */
/* PAGE INFO SECTION - TITLES AND STUDENT NAME */
/* ===================================================================== */

.portal-page-section {
  border: 1px solid rgba(255, 255, 255, 0.2);
  border-radius: 16px;
  padding: 1rem 1.5rem;
  margin-bottom: 1rem;
  background: rgba(255, 255, 255, 0.1);
  backdrop-filter: blur(10px);
}

.portal-page-title {
  color: white;
  font-size: 1.75rem;
  font-weight: 700;
  margin: 0 0 0.75rem;
  letter-spacing: -0.02em;
  line-height: 1.2;
}

.portal-student-name {
  color: white;
  padding: 0.4rem 1rem;
  border-radius: 12px;
  font-size: 1rem;
  font-weight: 600;
  display: inline-block;
  border: 1px solid rgba(255, 255, 255, 0.2);
  background: rgba(255, 255, 255, 0.15);
  backdrop-filter: blur(5px);
}

/* ===================================================================== */
/* PROGRESS PILLS - HUB VIEW STATS */
/* ===================================================================== */

.portal-progress-pills {
  display: flex;
  justify-content: center;
  gap: 0.75rem;
  flex-wrap: wrap;
}

.portal-progress-pill {
  border: 1px solid rgba(255, 255, 255, 0.25);
  border-radius: 20px;
  padding: 0.5rem 1rem;
  display: flex;
  align-items: center;
  gap: 0.4rem;
  font-size: 0.85rem;
  color: white;
  font-weight: 600;
  background: rgba(255, 255, 255, 0.15);
  backdrop-filter: blur(5px);
}

.portal-pill-icon {
  flex-shrink: 0;
  opacity: 0.9;
}

.portal-pill-text {
  white-space: nowrap;
}

/* ===================================================================== */
/* MAIN CONTENT AREA - APPLE-STYLE LAYOUT */
/* ===================================================================== */

.portal-main-content {
  padding: 2rem 1rem;
  max-width: 1200px;
  margin: 0 auto;
  background: var(--bg-primary);
  min-height: 60vh;
}

/* ===================================================================== */
/* FOOTER SECTION - CLEAN LOGOUT */
/* ===================================================================== */

.portal-footer {
  padding: 0 1rem 2rem;
  max-width: 1200px;
  margin: 0 auto;
  text-align: center;
  background: var(--bg-primary);
}

.portal-footer-content {
  display: flex;
  justify-content: center;
}

.portal-logout-button {
  display: inline-flex;
  align-items: center;
  gap: 0.5rem;
  padding: 0.75rem 1.5rem;
  border: 1px solid var(--border-primary);
  border-radius: 16px;
  background: var(--bg-secondary);
  color: var(--text-secondary);
  font-weight: 500;
  font-size: 0.95rem;
  cursor: pointer;
  transition: all 0.2s ease;
  box-shadow: var(--shadow-sm);
  font-family: inherit;
}

.portal-logout-button:hover {
  background: var(--bg-tertiary);
  border-color: var(--border-secondary);
  color: var(--text-primary);
  transform: translateY(-1px);
  box-shadow: var(--shadow-md);
}

.portal-logout-button:active {
  transform: translateY(0);
  box-shadow: var(--shadow-sm);
}

/* ===================================================================== */
/* CSS VARIABLES - THEME INTEGRATION */
/* ===================================================================== */

/* Light Theme Variables */
:root {
  --text-primary: #1c1917;
  --text-secondary: #78716c;
  --bg-primary: #f8fafc;
  --bg-secondary: #ffffff;
  --bg-tertiary: #f1f5f9;
  --bg-gradient: linear-gradient(135deg, #f8fafc 0%, #f1f5f9 100%);
  --border-primary: #e7e5e4;
  --border-secondary: #d6d3d1;
  --shadow-sm: 0 2px 8px rgba(0, 0, 0, 0.06);
  --shadow-md: 0 4px 16px rgba(0, 0, 0, 0.08);
  --brand-blue: #0082FF;
}

/* Dark Theme Variables */
.dark {
  --text-primary: #f8fafc;
  --text-secondary: #cbd5e1;
  --bg-primary: #0f172a;
  --bg-secondary: #1e293b;
  --bg-tertiary: #334155;
  --bg-gradient: linear-gradient(135deg, #0f172a 0%, #1e293b 100%);
  --border-primary: #334155;
  --border-secondary: #475569;
  --shadow-sm: 0 2px 8px rgba(0, 0, 0, 0.3);
  --shadow-md: 0 4px 16px rgba(0, 0, 0, 0.4);
}

/* ===================================================================== */
/* ANIMATIONS */
/* ===================================================================== */

@keyframes portal-spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

/* ===================================================================== */
/* RESPONSIVE DESIGN - MOBILE FIRST */
/* ===================================================================== */

/* Tablet Optimizations */
@media (max-width: 768px) {
  .portal-header {
    padding: 1.25rem 1rem 1.75rem;
  }
  
  .portal-academy-badge {
    font-size: 1.1rem;
    padding: 0.65rem 1.25rem;
  }
  
  .portal-page-title {
    font-size: 1.5rem;
  }
  
  .portal-main-content {
    padding: 1.5rem 1rem;
  }
  
  .portal-progress-pills {
    gap: 0.5rem;
  }
  
  .portal-progress-pill {
    font-size: 0.8rem;
    padding: 0.4rem 0.8rem;
  }
}

/* Mobile Optimizations - FIXED VERSION */
@media (max-width: 480px) {
  .portal-header {
    padding: 1rem 0.75rem 1.5rem;
  }
  
  .portal-header-content {
    padding: 0 0.5rem;
  }
  
  /* 🎯 BRANDING STAYS VERTICAL - Academy badge + Beta badge stacked */
  .portal-branding-section {
    flex-direction: column;
    align-items: center;
    gap: 0.75rem;
  }
  
  .portal-academy-badge {
    font-size: 1rem;
    padding: 0.6rem 1rem;
  }
  
  .portal-page-title {
    font-size: 1.25rem;
  }
  
  .portal-main-content {
    padding: 1rem 0.75rem;
  }
  
  .portal-back-button {
    font-size: 0.85rem;
    padding: 0.4rem;
  }
  
  /* 🎯 PROGRESS PILLS STAY HORIZONTAL - This is what Alex wanted! */
  .portal-progress-pills {
    display: flex;
    justify-content: center;
    gap: 0.5rem;
    flex-wrap: wrap;
  }
  
  .portal-progress-pill {
    font-size: 0.75rem;
    padding: 0.35rem 0.75rem;
  }
  
  .portal-logout-button {
    font-size: 0.9rem;
    padding: 0.65rem 1.25rem;
  }
}

/* ===================================================================== */
/* ACCESSIBILITY & FOCUS STATES */
/* ===================================================================== */

/* Focus states for keyboard navigation */
.portal-back-button:focus,
.portal-logout-button:focus {
  outline: 2px solid var(--brand-blue);
  outline-offset: 2px;
}

/* Reduced motion for accessibility */
@media (prefers-reduced-motion: reduce) {
  .portal-loading-spinner {
    animation: none;
  }
  
  .portal-back-button,
  .portal-logout-button,
  .portal-progress-pill,
  .poratal-student-name,
  .portal-academy-badge {
    transition: none;
  }
}