/* lesson.css - FIXED LIGHT MODE - A2 lessons now look great in both modes! */
/* 🎨 Apple-inspired design with glassmorphism A2 components */

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, "SF Pro Display", sans-serif;
  line-height: 1.6;
  transition: all 0.3s ease;
  font-feature-settings: "kern" 1, "liga" 1;
  text-rendering: optimizeLegibility;
}

/* ===== THEME VARIABLES - FIXED LIGHT MODE ===== */
:root {
  /* LIGHT MODE DEFAULTS */
  --bg-primary: rgba(255, 255, 255, 0.9);
  --bg-secondary: rgba(255, 255, 255, 0.95);
  --bg-tertiary: rgba(248, 250, 252, 0.8);
  --text-primary: #1e293b;
  --text-secondary: #475569;
  --text-muted: #64748b;
  --border-primary: rgba(148, 163, 184, 0.3);
  --border-secondary: rgba(148, 163, 184, 0.2);
  --shadow-sm: 0 4px 16px rgba(0, 0, 0, 0.1);
  --shadow-md: 0 8px 32px rgba(0, 0, 0, 0.15);
  --shadow-lg: 0 16px 48px rgba(0, 0, 0, 0.2);
  --shadow-xl: 0 24px 64px rgba(0, 0, 0, 0.25);
  --radius-sm: 8px;
  --radius-md: 12px;
  --radius-lg: 16px;
  --radius-xl: 20px;
  
  /* Light mode background */
  background: 
    radial-gradient(circle at 20% 80%, rgba(14, 165, 233, 0.1) 0%, transparent 50%),
    radial-gradient(circle at 80% 20%, rgba(168, 85, 247, 0.1) 0%, transparent 50%),
    radial-gradient(circle at 40% 40%, rgba(236, 72, 153, 0.1) 0%, transparent 50%),
    linear-gradient(135deg, #f8fafc 0%, #e2e8f0 50%, #f1f5f9 100%);
  color: #1e293b;
}

.dark {
  /* DARK MODE OVERRIDES */
  --bg-primary: rgba(255, 255, 255, 0.05);
  --bg-secondary: rgba(255, 255, 255, 0.1);
  --bg-tertiary: rgba(255, 255, 255, 0.05);
  --text-primary: #ffffff;
  --text-secondary: rgba(255, 255, 255, 0.8);
  --text-muted: rgba(255, 255, 255, 0.6);
  --border-primary: rgba(255, 255, 255, 0.2);
  --border-secondary: rgba(255, 255, 255, 0.1);
  --shadow-sm: 0 4px 16px rgba(0, 0, 0, 0.3);
  --shadow-md: 0 8px 32px rgba(0, 0, 0, 0.4);
  --shadow-lg: 0 16px 48px rgba(0, 0, 0, 0.5);
  --shadow-xl: 0 24px 64px rgba(0, 0, 0, 0.6);
  
  /* Dark mode background */
  background: 
    radial-gradient(circle at 20% 80%, rgba(14, 165, 233, 0.08) 0%, transparent 50%),
    radial-gradient(circle at 80% 20%, rgba(168, 85, 247, 0.08) 0%, transparent 50%),
    radial-gradient(circle at 40% 40%, rgba(236, 72, 153, 0.08) 0%, transparent 50%),
    linear-gradient(135deg, #1a1a2e 0%, #16213e 50%, #0f0f23 100%);
  color: #ffffff;
}

/* ===== ELEGANT LOADING ===== */
.elegant-loading {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-height: 50vh;
  text-align: center;
}

.loading-pulse {
  width: 60px;
  height: 60px;
  border-radius: 50%;
  background: linear-gradient(135deg, #3b82f6, #8b5cf6);
  margin-bottom: 2rem;
  animation: pulse 2s infinite;
}

@keyframes pulse {
  0%, 100% { transform: scale(1); opacity: 1; }
  50% { transform: scale(1.1); opacity: 0.7; }
}

.loading-title {
  font-size: 1.75rem;
  font-weight: 700;
  color: var(--text-primary);
  margin-bottom: 0.5rem;
  letter-spacing: -0.02em;
}

.loading-subtitle {
  font-size: 1rem;
  color: var(--text-secondary);
  font-weight: 400;
}

.lesson-a2-container,
.lessons-container,
.selector-container {
  max-width: 1000px;
  margin: 0 auto;
  padding: 20px;
  min-height: 100vh;
  background: transparent;
}

/* ===== GLASS HEADERS ===== */
.lesson-a2-header,
.lessons-header,
.selector-header {
  background: var(--bg-primary);
  backdrop-filter: blur(20px);
  border: 1px solid var(--border-primary);
  border-radius: 20px;
  padding: 24px;
  margin-bottom: 2rem;
  box-shadow: var(--shadow-md);
}

.lesson-a2-breadcrumb {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  color: var(--text-secondary);
  font-size: 0.9rem;
  margin-bottom: 1rem;
}

.lessons-back-btn {
  display: inline-flex;
  align-items: center;
  gap: 0.5rem;
  color: #60a5fa;
  text-decoration: none;
  font-weight: 500;
  padding: 8px 12px;
  border-radius: 8px;
  transition: all 0.2s ease;
  border: 1px solid var(--border-secondary);
  background: var(--bg-tertiary);
  backdrop-filter: blur(10px);
  cursor: pointer;
}

.lessons-back-btn:hover {
  background: var(--bg-secondary);
  transform: translateY(-1px);
  color: #93c5fd;
  box-shadow: var(--shadow-sm);
}

.lesson-a2-title,
.lessons-title,
.selector-title {
  font-size: 2rem;
  font-weight: 700;
  color: var(--text-primary);
  margin-bottom: 0.5rem;
}

.lesson-a2-level-badge {
  display: inline-flex;
  align-items: center;
  padding: 4px 12px;
  background: linear-gradient(135deg, #3b82f6, #1d4ed8);
  color: white;
  border-radius: 20px;
  font-size: 0.8rem;
  font-weight: 600;
  box-shadow: 0 4px 12px rgba(59, 130, 246, 0.3);
}

.lesson-a2-meta {
  display: flex;
  gap: 1rem;
  flex-wrap: wrap;
  color: var(--text-secondary);
  font-size: 0.9rem;
}

.lesson-a2-meta-item-small {
  display: flex;
  align-items: center;
  gap: 0.25rem;
}

.selector-subtitle {
  color: var(--text-secondary);
  margin-bottom: 1.5rem;
}

/* ===== GLASS STATS GRID ===== */
.compact-stats-grid {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 1rem;
  margin: 1.5rem 0;
}

.compact-stat {
  text-align: center;
  padding: 1rem;
  background: var(--bg-secondary);
  backdrop-filter: blur(15px);
  border: 1px solid var(--border-primary);
  border-radius: var(--radius-md);
  box-shadow: var(--shadow-sm);
}

.compact-stat .stat-number {
  font-size: 1.5rem;
  font-weight: 800;
  color: var(--text-primary);
  display: block;
  line-height: 1;
}

.compact-stat .stat-label {
  font-size: 0.75rem;
  color: var(--text-secondary);
  font-weight: 600;
  text-transform: uppercase;
  letter-spacing: 0.05em;
  margin-top: 0.25rem;
}

/* ===== GLASS SERIES GRID ===== */
.compact-series-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(320px, 1fr));
  gap: 1.5rem;
}

.compact-series-card {
  background: var(--bg-secondary);
  backdrop-filter: blur(20px);
  border: 1px solid var(--border-primary);
  border-radius: var(--radius-md);
  padding: 1.5rem;
  transition: all 0.3s ease;
  cursor: pointer;
  box-shadow: var(--shadow-sm);
  position: relative;
  overflow: hidden;
}

.compact-series-card::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 3px;
  background: var(--series-gradient);
}

.compact-series-card:hover {
  transform: translateY(-4px);
  background: var(--bg-primary);
  box-shadow: var(--shadow-md);
  border-color: var(--border-primary);
}

.compact-series-header {
  display: flex;
  align-items: flex-start;
  gap: 1rem;
  margin-bottom: 1rem;
}

.series-icon-compact {
  width: 48px;
  height: 48px;
  border-radius: var(--radius-sm);
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 1.5rem;
  flex-shrink: 0;
  background: var(--bg-tertiary);
  backdrop-filter: blur(10px);
}

.series-info-compact {
  flex: 1;
  min-width: 0;
}

.compact-series-title {
  font-size: 1.2rem;
  font-weight: 700;
  color: var(--text-primary);
  margin: 0 0 0.25rem 0;
  line-height: 1.3;
}

.compact-series-subtitle {
  font-size: 0.85rem;
  color: var(--text-secondary);
  font-weight: 500;
  margin: 0;
}

.series-meta-compact {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  gap: 0.25rem;
}

.difficulty-badge {
  padding: 0.25rem 0.5rem;
  border-radius: 0.75rem;
  font-size: 0.7rem;
  font-weight: 600;
  text-transform: uppercase;
  letter-spacing: 0.05em;
  background: var(--bg-tertiary);
  color: var(--text-primary);
  border: 1px solid var(--border-secondary);
}

.compact-series-description {
  font-size: 0.9rem;
  color: var(--text-secondary);
  line-height: 1.5;
  margin: 0 0 1.25rem 0;
}

.compact-progress-section {
  margin-bottom: 1.25rem;
}

.progress-info {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 0.5rem;
}

.progress-text {
  font-size: 0.85rem;
  color: var(--text-primary);
  font-weight: 600;
}

.progress-percentage {
  font-size: 0.85rem;
  font-weight: 700;
  color: var(--text-secondary);
}

.xp-badge-compact {
  padding: 0.2rem 0.5rem;
  border-radius: 0.75rem;
  font-size: 0.7rem;
  font-weight: 700;
  background: var(--bg-tertiary);
  color: var(--text-primary);
  border: 1px solid var(--border-secondary);
}

.compact-progress-bar {
  width: 100%;
  height: 6px;
  background: var(--bg-tertiary);
  border-radius: 3px;
  overflow: hidden;
  border: 1px solid var(--border-secondary);
}

.compact-progress-fill {
  height: 100%;
  border-radius: 3px;
  transition: width 0.4s ease;
}

.compact-series-btn {
  width: 100%;
  padding: 0.75rem 1rem;
  border: none;
  border-radius: var(--radius-sm);
  font-weight: 600;
  font-size: 0.9rem;
  cursor: pointer;
  transition: all 0.2s ease;
  margin-bottom: 1rem;
  background: var(--bg-tertiary);
  color: var(--text-primary);
  backdrop-filter: blur(10px);
  border: 1px solid var(--border-secondary);
}

.compact-series-btn:hover {
  transform: translateY(-1px);
  background: var(--bg-secondary);
  box-shadow: var(--shadow-sm);
  border-color: var(--border-primary);
}

.series-details-compact {
  display: flex;
  justify-content: space-between;
  gap: 0.5rem;
  padding-top: 1rem;
  border-top: 1px solid var(--border-secondary);
}

.detail-item {
  flex: 1;
  text-align: center;
}

.detail-item span {
  font-size: 0.75rem;
  color: var(--text-secondary);
  font-weight: 500;
}

/* ===== GLASS LESSONS GRID ===== */
.compact-lessons-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  gap: 1rem;
}

.compact-lesson-card {
  background: var(--bg-secondary);
  border: 2px solid var(--border-primary);
  backdrop-filter: blur(20px);
  border-radius: var(--radius-md);
  padding: 2.25rem;
  transition: all 0.3s ease;
  cursor: pointer;
  box-shadow: var(--shadow-sm);
  position: relative;
  overflow: hidden;
}

.compact-lesson-card::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 3px;
  background: var(--series-gradient);
}

.compact-lesson-card:hover:not(.locked) {
  transform: translateY(-8px);
  background: var(--bg-primary);
  box-shadow: var(--shadow-md);
  border-color: var(--border-primary);
}

.compact-lesson-card.locked {
  cursor: not-allowed;
  opacity: 0.5;
}

.compact-lesson-card.completed::after {
  content: '✓';
  position: absolute;
  top: 1rem;
  right: 1rem;
  width: 24px;
  height: 24px;
  background: linear-gradient(135deg, #10b981, #059669);
  color: white;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 0.75rem;
  font-weight: 700;
  box-shadow: 0 4px 12px rgba(16, 185, 129, 0.3);
}

.compact-lesson-header {
  display: flex;
  align-items: flex-start;
  gap: 1rem;
  margin-bottom: 1rem;
}

.compact-lesson-number {
  width: 36px;
  height: 36px;
  border-radius: var(--radius-sm);
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: 700;
  font-size: 0.9rem;
  color: white;
  flex-shrink: 0;
  background: var(--bg-tertiary);
  backdrop-filter: blur(10px);
  border: 1px solid var(--border-primary);
}

.compact-lesson-info {
  flex: 1;
  min-width: 0;
}

.compact-lesson-title {
  font-size: 1rem;
  font-weight: 700;
  color: var(--text-primary);
  margin: 0 0 0.25rem 0;
  line-height: 1.3;
}

.compact-lesson-description {
  font-size: 0.8rem;
  color: var(--text-secondary);
  line-height: 1.4;
  margin: 0;
}

.compact-lesson-status {
  font-size: 1.2rem;
  flex-shrink: 0;
}

.compact-lesson-meta {
  display: flex;
  gap: 1rem;
  flex-wrap: wrap;
  margin-bottom: 1rem;
}

.compact-lesson-meta .meta-item {
  display: flex;
  align-items: center;
  gap: 0.25rem;
  font-size: 0.75rem;
  color: var(--text-secondary);
  font-weight: 500;
  background: var(--bg-tertiary);
  padding: 0.25rem 0.5rem;
  border-radius: 6px;
  border: 1px solid var(--border-secondary);
}

.compact-lesson-footer {
  border-top: 1px solid var(--border-secondary);
  padding-top: 0.75rem;
  margin-top: 0.75rem;
  background: var(--bg-tertiary);
  margin: 0.75rem -1.25rem -1.25rem;
  padding: 0.75rem 1.25rem;
  border-radius: 0 0 var(--radius-md) var(--radius-md);
}

.footer-locked {
  text-align: center;
  color: var(--text-muted);
  font-size: 0.75rem;
}

.footer-completed {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.completion-stats-compact {
  display: flex;
  gap: 0.5rem;
}

.score-badge-compact {
  padding: 0.2rem 0.4rem;
  border-radius: 0.5rem;
  font-size: 0.7rem;
  font-weight: 600;
  background: var(--bg-secondary);
  color: var(--text-primary);
  border: 1px solid var(--border-secondary);
}

.completion-text {
  font-size: 0.75rem;
  font-weight: 600;
  color: #10b981;
}

.footer-available {
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size: 0.8rem;
  font-weight: 500;
  color: var(--text-secondary);
}

.start-text {
  color: var(--text-secondary);
}

/* ===== GLASS SERIES SUMMARY ===== */
.compact-series-summary {
  background: var(--bg-secondary);
  backdrop-filter: blur(20px);
  border: 1px solid var(--border-primary);
  border-radius: var(--radius-md);
  padding: 1.5rem;
  margin-top: 2rem;
  box-shadow: var(--shadow-sm);
}

.summary-content-compact {
  display: flex;
  align-items: center;
  gap: 1rem;
  margin-bottom: 1rem;
}

.summary-icon-compact {
  font-size: 2rem;
  flex-shrink: 0;
}

.summary-text-compact {
  flex: 1;
}

.summary-title-compact {
  font-size: 1.1rem;
  font-weight: 700;
  color: var(--text-primary);
  margin: 0 0 0.25rem 0;
}

.summary-description-compact {
  font-size: 0.85rem;
  color: var(--text-secondary);
  line-height: 1.5;
  margin: 0;
}

.summary-progress-compact {
  display: flex;
  align-items: center;
  gap: 1rem;
}

.summary-progress-compact .compact-progress-bar {
  flex: 1;
}

.summary-percentage {
  font-size: 0.8rem;
  font-weight: 600;
  color: var(--text-primary);
  flex-shrink: 0;
}

/* ===== GLASS INFO FOOTER ===== */
.compact-info-footer {
  background: var(--bg-secondary);
  backdrop-filter: blur(20px);
  border: 1px solid var(--border-primary);
  border-radius: var(--radius-lg);
  padding: 2rem;
  margin-top: 2rem;
  text-align: center;
  box-shadow: var(--shadow-sm);
}

.footer-content h3 {
  font-size: 1.25rem;
  font-weight: 700;
  color: var(--text-primary);
  margin: 0 0 1rem 0;
}

.footer-content p {
  font-size: 1rem;
  color: var(--text-secondary);
  line-height: 1.6;
  margin: 0 0 1.5rem 0;
  max-width: 600px;
  margin-left: auto;
  margin-right: auto;
}

.footer-features {
  display: flex;
  justify-content: center;
  gap: 2rem;
  flex-wrap: wrap;
}

.footer-features span {
  font-size: 0.9rem;
  color: var(--text-secondary);
  font-weight: 600;
}

/* ===== GLASS QUESTIONS SECTION ===== */
.lesson-a2-questions-section {
  background: var(--bg-secondary);
  backdrop-filter: blur(20px);
  border: 1px solid var(--border-primary);
  border-radius: 12px;
  padding: 24px;
  margin-bottom: 2rem;
  box-shadow: var(--shadow-sm);
}

.lesson-a2-questions-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 1.5rem;
}

.lesson-a2-questions-title {
  font-size: 1.25rem;
  font-weight: 600;
  color: var(--text-primary);
}

.lesson-a2-progress {
  width: 100%;
  height: 8px;
  background: var(--bg-tertiary);
  border-radius: 4px;
  overflow: hidden;
  margin: 1rem 0;
  border: 1px solid var(--border-secondary);
}

.lesson-a2-progress-fill {
  height: 100%;
  background: #3b82f6;
  border-radius: 4px;
  transition: width 0.3s ease;
}

.lesson-a2-question-card {
  background: var(--bg-tertiary);
  backdrop-filter: blur(15px);
  border: 1px solid var(--border-secondary);
  border-radius: 12px;
  padding: 24px;
  margin-bottom: 1.5rem;
  box-shadow: var(--shadow-sm);
}

.lesson-a2-question {
  margin-bottom: 1.5rem;
}

.lesson-a2-question:last-child {
  margin-bottom: 0;
}

.lesson-a2-options {
  display: grid;
  gap: 0.75rem;
  margin-top: 0.75rem;
}

.lesson-a2-option {
  display: flex;
  align-items: center;
  padding: 16px;
  background: var(--bg-secondary);
  border: 2px solid var(--border-secondary);
  border-radius: 8px;
  cursor: pointer;
  transition: all 0.2s ease;
  font-weight: 500;
  text-align: left;
  width: 100%;
  color: var(--text-primary);
  backdrop-filter: blur(10px);
}

.lesson-a2-option:hover {
  border-color: #3b82f6;
  background: var(--bg-primary);
  transform: translateY(-1px);
  box-shadow: var(--shadow-sm);
}

.lesson-a2-option.selected {
  border-color: #3b82f6;
  background: #3b82f6;
  color: white;
}

.lesson-a2-submit {
  width: 100%;
  background: linear-gradient(135deg, #10b981, #059669);
  color: white;
  border: none;
  padding: 16px 32px;
  border-radius: 12px;
  font-size: 1.1rem;
  font-weight: 600;
  cursor: pointer;
  margin-top: 1rem;
  transition: all 0.2s ease;
  box-shadow: 0 4px 16px rgba(16, 185, 129, 0.3);
}

.lesson-a2-submit:hover {
  background: linear-gradient(135deg, #059669, #047857);
  transform: translateY(-2px);
  box-shadow: 0 8px 24px rgba(16, 185, 129, 0.4);
}

.lesson-a2-submit:disabled {
  background: var(--bg-tertiary);
  color: var(--text-muted);
  cursor: not-allowed;
  transform: none;
  box-shadow: none;
  border: 1px solid var(--border-secondary);
}

/* ===== GLASS RESULTS MODAL ===== */
.lesson-a2-results-modal {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.8);
  backdrop-filter: blur(10px);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1000;
  padding: 20px;
}

.lesson-a2-results-content {
  background: var(--bg-primary);
  backdrop-filter: blur(30px);
  border: 1px solid var(--border-primary);
  border-radius: 20px;
  padding: 32px;
  max-width: 500px;
  width: 100%;
  max-height: 80vh;
  overflow-y: auto;
  position: relative;
  box-shadow: var(--shadow-xl);
}

.lesson-results-container {
  text-align: center;
  color: var(--text-primary);
}

.lesson-results-emoji {
  font-size: 3rem;
  margin-bottom: 1rem;
  display: block;
}

.lesson-results-title {
  font-size: 1.75rem;
  font-weight: 700;
  color: var(--text-primary);
  margin-bottom: 0.5rem;
}

.lesson-results-message {
  color: var(--text-secondary);
  font-size: 1rem;
  margin-bottom: 2rem;
}

.lesson-score-circle {
  width: 100px;
  height: 100px;
  border-radius: 50%;
  background: linear-gradient(135deg, #3b82f6, #1d4ed8);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  color: white;
  margin: 0 auto 2rem;
  box-shadow: 0 8px 32px rgba(59, 130, 246, 0.4);
}

.lesson-score-percentage {
  font-size: 1.75rem;
  font-weight: 700;
}

.lesson-score-label {
  font-size: 0.8rem;
  opacity: 0.9;
}

.lesson-results-stats {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 1rem;
  margin: 2rem 0;
}

.lesson-stat-item {
  text-align: center;
  padding: 16px;
  background: var(--bg-secondary);
  backdrop-filter: blur(15px);
  border: 1px solid var(--border-primary);
  border-radius: 12px;
}

.lesson-stat-value {
  font-size: 1.5rem;
  font-weight: 700;
  color: var(--text-primary);
  display: block;
}

.lesson-stat-label {
  font-size: 0.8rem;
  color: var(--text-secondary);
  text-transform: uppercase;
  letter-spacing: 0.05em;
  font-weight: 600;
}

.lesson-results-actions {
  display: flex;
  gap: 1rem;
  margin-top: 2rem;
}

.lesson-retry-button,
.lesson-continue-button {
  flex: 1;
  padding: 12px 24px;
  border: none;
  border-radius: 12px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.2s ease;
  backdrop-filter: blur(15px);
}

.lesson-retry-button {
  background: var(--bg-secondary);
  color: var(--text-primary);
  border: 1px solid var(--border-primary);
}

.lesson-retry-button:hover {
  background: var(--bg-primary);
  transform: translateY(-1px);
  box-shadow: var(--shadow-sm);
}

.lesson-continue-button {
  background: linear-gradient(135deg, #3b82f6, #1d4ed8);
  color: white;
  border: 1px solid rgba(59, 130, 246, 0.3);
}

.lesson-continue-button:hover {
  background: linear-gradient(135deg, #2563eb, #1e40af);
  transform: translateY(-1px);
}

/* ===== MOBILE RESPONSIVE ===== */
@media (max-width: 768px) {
  .lesson-a2-container,
  .selector-container,
  .lessons-container {
    padding: 16px;
  }
  
  .lesson-a2-title,
  .selector-title,
  .lessons-title {
    font-size: 1.5rem;
  }
  
  .lesson-a2-results-content {
    padding: 24px;
    margin: 16px;
  }
  
  .lesson-results-stats {
    grid-template-columns: repeat(2, 1fr);
  }

  .lesson-results-actions {
    flex-direction: column;
  }

  .compact-stats-grid {
    grid-template-columns: repeat(2, 1fr);
    gap: 0.75rem;
  }
  
  .compact-series-grid {
    grid-template-columns: 1fr;
    gap: 1rem;
  }
  
  .compact-lessons-grid {
    grid-template-columns: 1fr;
    gap: 0.75rem;
  }
  
  .compact-series-card,
  .compact-lesson-card {
    padding: 1.25rem;
  }
  
  .compact-series-header,
  .compact-lesson-header {
    gap: 0.75rem;
  }
  
  .series-icon-compact {
    width: 40px;
    height: 40px;
    font-size: 1.25rem;
  }
  
  .compact-lesson-number {
    width: 32px;
    height: 32px;
    font-size: 0.8rem;
  }
  
  .compact-series-title,
  .compact-lesson-title {
    font-size: 1.1rem;
  }
}

/* ===== SERIES THEMES ===== */
.clean-audio-player[style*="--theme-color: #0ea5e9"] .progress-fill {
  background: linear-gradient(90deg, #0ea5e9, #0284c7);
  box-shadow: 0 0 16px rgba(14, 165, 233, 0.4);
}

.clean-audio-player[style*="--theme-color: #a855f7"] .progress-fill {
  background: linear-gradient(90deg, #a855f7, #9333ea);
  box-shadow: 0 0 16px rgba(168, 85, 247, 0.4);
}

.clean-audio-player[style*="--theme-color: #ec4899"] .progress-fill {
  background: linear-gradient(90deg, #ec4899, #db2777);
  box-shadow: 0 0 16px rgba(236, 72, 153, 0.4);
}

/* ===== DETAILED QUESTION REVIEW STYLES ===== */

/* Detailed Review Container */
.lesson-detailed-review {
  margin: 2rem 0;
  max-width: 100%;
}

.review-title {
  font-size: 1.2rem;
  font-weight: 700;
  color: var(--text-primary);
  margin-bottom: 1.5rem;
  text-align: center;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 0.5rem;
}

/* Questions Review List */
.questions-review-list {
  max-height: 400px;
  overflow-y: auto;
  padding-right: 0.5rem;
  margin-bottom: 1rem;
}

/* Custom scrollbar for review list */
.questions-review-list::-webkit-scrollbar {
  width: 6px;
}

.questions-review-list::-webkit-scrollbar-track {
  background: var(--bg-tertiary);
  border-radius: 3px;
}

.questions-review-list::-webkit-scrollbar-thumb {
  background: var(--border-primary);
  border-radius: 3px;
}

.questions-review-list::-webkit-scrollbar-thumb:hover {
  background: var(--text-secondary);
}

/* Individual Question Review Item */
.question-review-item {
  background: var(--bg-tertiary);
  border: 2px solid var(--border-secondary);
  border-radius: 12px;
  padding: 1.25rem;
  margin-bottom: 1rem;
  transition: all 0.3s ease;
  position: relative;
  overflow: hidden;
}

.question-review-item::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 3px;
  border-radius: 12px 12px 0 0;
}

.question-review-item.correct {
  border-color: rgba(16, 185, 129, 0.3);
  background: rgba(16, 185, 129, 0.05);
}

.question-review-item.correct::before {
  background: linear-gradient(90deg, #10b981, #059669);
}

.question-review-item.incorrect {
  border-color: rgba(239, 68, 68, 0.3);
  background: rgba(239, 68, 68, 0.05);
}

.question-review-item.incorrect::before {
  background: linear-gradient(90deg, #ef4444, #dc2626);
}

/* Question Header */
.question-review-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 1rem;
}

.question-number {
  display: flex;
  align-items: center;
  gap: 0.75rem;
}

.status-icon {
  font-size: 1.25rem;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 32px;
  height: 32px;
  border-radius: 50%;
  font-weight: 700;
}

.status-icon.correct {
  background: rgba(16, 185, 129, 0.15);
  color: #10b981;
}

.status-icon.incorrect {
  background: rgba(239, 68, 68, 0.15);
  color: #ef4444;
}

.question-label {
  font-weight: 700;
  font-size: 1rem;
  color: var(--text-primary);
}

.points-badge {
  padding: 0.25rem 0.75rem;
  border-radius: 20px;
  font-size: 0.8rem;
  font-weight: 700;
  letter-spacing: 0.5px;
}

.points-badge.earned {
  background: rgba(16, 185, 129, 0.15);
  color: #10b981;
  border: 1px solid rgba(16, 185, 129, 0.3);
}

.points-badge.missed {
  background: rgba(239, 68, 68, 0.15);
  color: #ef4444;
  border: 1px solid rgba(239, 68, 68, 0.3);
}

/* Question Text */
.question-text {
  font-size: 1rem;
  font-weight: 600;
  color: var(--text-primary);
  line-height: 1.5;
  margin-bottom: 1rem;
  padding: 0.75rem;
  background: var(--bg-secondary);
  border-radius: 8px;
  border-left: 4px solid var(--border-primary);
}

/* Answer Details */
.answer-details {
  margin-bottom: 1rem;
}

.answer-row {
  display: flex;
  align-items: flex-start;
  gap: 0.75rem;
  margin-bottom: 0.5rem;
  flex-wrap: wrap;
}

.answer-label {
  font-weight: 600;
  color: var(--text-secondary);
  font-size: 0.9rem;
  min-width: 120px;
  flex-shrink: 0;
}

.answer-value {
  flex: 1;
  padding: 0.5rem 0.75rem;
  border-radius: 6px;
  font-weight: 500;
  font-size: 0.9rem;
  min-width: 0;
  word-wrap: break-word;
}

.answer-value.correct-answer {
  background: rgba(16, 185, 129, 0.1);
  color: #059669;
  border: 1px solid rgba(16, 185, 129, 0.2);
}

.answer-value.wrong-answer {
  background: rgba(239, 68, 68, 0.1);
  color: #dc2626;
  border: 1px solid rgba(239, 68, 68, 0.2);
}

/* Feedback Message */
.feedback-message {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  padding: 0.75rem;
  border-radius: 8px;
  font-size: 0.9rem;
  font-weight: 500;
}

.feedback-message.success {
  background: rgba(16, 185, 129, 0.1);
  color: #059669;
  border: 1px solid rgba(16, 185, 129, 0.2);
}

.feedback-message.error {
  background: rgba(245, 158, 11, 0.1);
  color: #d97706;
  border: 1px solid rgba(245, 158, 11, 0.2);
}

.feedback-icon {
  font-size: 1.1rem;
  flex-shrink: 0;
}

/* Level Up Banner */
.level-up-banner {
  background: linear-gradient(135deg, #f59e0b, #d97706);
  border-radius: 12px;
  padding: 1.5rem;
  margin: 1.5rem 0;
  color: white;
  text-align: center;
  animation: levelUpPulse 2s ease-in-out infinite alternate;
}

@keyframes levelUpPulse {
  from { box-shadow: 0 0 20px rgba(245, 158, 11, 0.4); }
  to { box-shadow: 0 0 30px rgba(245, 158, 11, 0.6); }
}

.level-up-content {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 1rem;
}

.level-up-icon {
  font-size: 2rem;
  animation: bounce 1s ease-in-out infinite;
}

.level-up-text h4 {
  font-size: 1.2rem;
  margin-bottom: 0.25rem;
}

.level-up-text p {
  font-size: 1rem;
  opacity: 0.9;
}

/* Mobile Responsive */
@media (max-width: 768px) {
  .lesson-a2-results-content {
    margin: 1rem;
    padding: 1.5rem;
    max-height: 90vh;
  }
  
  .questions-review-list {
    max-height: 300px;
  }
  
  .question-review-item {
    padding: 1rem;
  }
  
  .question-review-header {
    flex-direction: column;
    align-items: flex-start;
    gap: 0.5rem;
  }
  
  .answer-row {
    flex-direction: column;
    gap: 0.25rem;
  }
  
  .answer-label {
    min-width: auto;
    font-size: 0.85rem;
  }
  
  .answer-value {
    font-size: 0.85rem;
  }
  
  .level-up-content {
    flex-direction: column;
    text-align: center;
  }
  
  .review-title {
    font-size: 1.1rem;
  }
}

/* Dark Mode Specific Overrides */
.dark .question-text {
  background: rgba(255, 255, 255, 0.05);
  border-left-color: rgba(255, 255, 255, 0.2);
}

.dark .answer-value.correct-answer {
  background: rgba(16, 185, 129, 0.15);
  color: #34d399;
}

.dark .answer-value.wrong-answer {
  background: rgba(239, 68, 68, 0.15);
  color: #f87171;
}

.dark .feedback-message.success {
  background: rgba(16, 185, 129, 0.15);
  color: #34d399;
}

.dark .feedback-message.error {
  background: rgba(245, 158, 11, 0.15);
  color: #fbbf24;
}

/* Smooth Animations */
.question-review-item {
  animation: slideInUp 0.3s ease-out;
  animation-fill-mode: both;
}

.question-review-item:nth-child(1) { animation-delay: 0.1s; }
.question-review-item:nth-child(2) { animation-delay: 0.2s; }
.question-review-item:nth-child(3) { animation-delay: 0.3s; }
.question-review-item:nth-child(4) { animation-delay: 0.4s; }
.question-review-item:nth-child(5) { animation-delay: 0.5s; }

@keyframes slideInUp {
  from {
    opacity: 0;
    transform: translateY(20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}