/* src/components/student/views/QuizPlayer/quiz.css */
/* 🎯 QUIZ PLAYER STYLES - MODERN UI/UX TRANSFORMATION */

/* Container & Layout */
.quiz-player-container {
  max-width: 1000px;
  margin: 0 auto;
  padding: 0;
  min-height: 100vh;
}

.quiz-back-section {
  text-align: center;
  margin-top: 2rem;
  padding: 0 1rem 2rem;
}

/* Loading States */
.quiz-loading-container {
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
}

.quiz-loading-content {
  text-align: center;
}

.quiz-loading-spinner {
  width: 64px;
  height: 64px;
  border: 4px solid var(--border-primary);
  border-top: 4px solid #0082FF;
  border-radius: 50%;
  animation: quiz-spin 1s linear infinite;
  margin: 0 auto 2rem;
  position: relative;
}

.quiz-loading-spinner::after {
  content: '';
  position: absolute;
  top: 8px;
  left: 8px;
  right: 8px;
  bottom: 8px;
  border: 2px solid transparent;
  border-top: 2px solid #1e40af;
  border-radius: 50%;
  animation: quiz-spin 1.5s linear infinite reverse;
}

.quiz-loading-text {
  font-size: 1.25rem;
  font-weight: 600;
  opacity: 0.9;
  color: var(--text-primary);
}

/* Quiz Selector - MODERN DESIGN */
.quiz-selector {
  background: var(--bg-secondary);
  border-radius: 0 0 24px 24px;
  padding: 2.5rem 2rem;
  margin-bottom: 2rem;
  box-shadow: var(--shadow-lg);
  border: 1px solid var(--border-primary);
  position: relative;
  overflow: hidden;
}

.quiz-selector::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 4px;
  background: linear-gradient(90deg, #0082FF, #1e40af);
}

.quiz-progress-overview {
  text-align: center;
  margin-bottom: 2.5rem;
}

.quiz-progress-title {
  font-size: 2rem;
  font-weight: 700;
  color: var(--text-primary);
  margin-bottom: 0.75rem;
  letter-spacing: -0.02em;
  background: linear-gradient(135deg, #0082FF, #1e40af);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

.quiz-progress-subtitle {
  color: var(--text-secondary);
  margin-bottom: 2rem;
  font-size: 1.1rem;
  font-weight: 400;
}

.quiz-progress-stats {
  display: flex;
  justify-content: center;
  gap: 2rem;
  margin-bottom: 2rem;
}

.quiz-stat-pill {
  background: var(--bg-tertiary);
  color: var(--text-primary);
  padding: 1rem 1.5rem;
  border-radius: 16px;
  font-size: 0.9rem;
  font-weight: 600;
  border: 1px solid var(--border-primary);
  box-shadow: var(--shadow-sm);
  transition: all 0.2s ease;
  backdrop-filter: blur(10px);
}

.quiz-stat-pill:hover {
  transform: translateY(-2px);
  box-shadow: var(--shadow-md);
}

.quiz-progress-bar-container {
  margin-bottom: 1.5rem;
  background: var(--bg-tertiary);
  border-radius: 12px;
  padding: 0.5rem;
  border: 1px solid var(--border-primary);
}

/* Quiz Grid - MODERN CARDS */
.quiz-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
  gap: 1.5rem;
  padding: 0 1rem;
}

.quiz-card {
  background: var(--bg-tertiary);
  border: 2px solid var(--border-primary);
  border-radius: 20px;
  padding: 1.5rem;
  cursor: pointer;
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  position: relative;
  overflow: hidden;
  min-height: 200px;
  display: flex;
  flex-direction: column;
}

.quiz-card::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 4px;
  background: linear-gradient(90deg, transparent, transparent);
  transition: all 0.3s ease;
}

.quiz-card:hover {
  transform: translateY(-8px);
  box-shadow: 0 12px 40px rgba(0, 0, 0, 0.15);
  border-color: #0082FF;
}

.quiz-card:hover::before {
  background: linear-gradient(90deg, #0082FF, #1e40af);
}

.quiz-card.completed {
  background: linear-gradient(135deg, #0082FF, #1e40af);
  border-color: #0082FF;
  color: white;
  box-shadow: 0 4px 20px rgba(0, 130, 255, 0.3);
}

.quiz-card.completed::before {
  background: linear-gradient(90deg, rgba(255, 255, 255, 0.3), rgba(255, 255, 255, 0.1));
}

/* Quiz Card Content */
.quiz-card-icon-container {
  text-align: center;
  margin-bottom: 1rem;
}

.quiz-card-icon {
  font-size: 3rem;
  display: block;
  transition: transform 0.3s ease;
}

.quiz-card:hover .quiz-card-icon {
  transform: scale(1.1) rotate(5deg);
}

.quiz-card-content {
  flex: 1;
  text-align: center;
}

.quiz-card-title {
  font-size: 1.1rem;
  font-weight: 700;
  margin-bottom: 0.5rem;
  color: var(--text-primary);
  line-height: 1.3;
}

.quiz-card.completed .quiz-card-title {
  color: white;
}

.quiz-card-description {
  font-size: 0.85rem;
  opacity: 0.8;
  color: var(--text-secondary);
  margin-bottom: 1.5rem;
  font-weight: 500;
  line-height: 1.4;
}

.quiz-card.completed .quiz-card-description {
  color: rgba(255, 255, 255, 0.9);
}

/* Quiz Stats - Modern Layout */
.quiz-card-stats {
  display: flex;
  justify-content: space-between;
  gap: 0.75rem;
  margin-bottom: 1.5rem;
  padding: 1rem;
  background: rgba(255, 255, 255, 0.05);
  border-radius: 12px;
  border: 1px solid var(--border-secondary);
}

.quiz-card.completed .quiz-card-stats {
  background: rgba(255, 255, 255, 0.15);
  border-color: rgba(255, 255, 255, 0.2);
}

.quiz-stat {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  flex: 1;
  text-align: center;
  justify-content: center;
}

.quiz-stat-icon {
  font-size: 1rem;
  opacity: 0.8;
}

.quiz-stat-info {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.quiz-stat-value {
  font-weight: 600;
  color: var(--text-primary);
  font-size: 0.9rem;
  margin-bottom: 0.1rem;
}

.quiz-card.completed .quiz-stat-value {
  color: white;
}

.quiz-stat-label {
  color: var(--text-secondary);
  font-size: 0.75rem;
  opacity: 0.8;
}

.quiz-card.completed .quiz-stat-label {
  color: rgba(255, 255, 255, 0.8);
}

/* Action Button */
.quiz-card-action {
  margin-top: auto;
}

.quiz-start-button {
  width: 100%;
  padding: 0.875rem 1.5rem;
  border: none;
  border-radius: 12px;
  font-weight: 600;
  font-size: 0.9rem;
  cursor: pointer;
  transition: all 0.2s ease;
  background: linear-gradient(135deg, #0082FF, #1e40af);
  color: white;
  box-shadow: 0 2px 8px rgba(0, 130, 255, 0.3);
}

.quiz-card.completed .quiz-start-button {
  background: rgba(255, 255, 255, 0.2);
  border: 1px solid rgba(255, 255, 255, 0.3);
  color: white;
}

.quiz-start-button:hover {
  transform: translateY(-1px);
  filter: brightness(1.05);
  box-shadow: 0 4px 16px rgba(0, 130, 255, 0.4);
}

.quiz-card.completed .quiz-start-button:hover {
  background: rgba(255, 255, 255, 0.3);
  box-shadow: 0 4px 16px rgba(255, 255, 255, 0.2);
}

/* Completion Badge */
.quiz-completion-badge {
  position: absolute;
  top: 1rem;
  right: 1rem;
  background: rgba(255, 255, 255, 0.9);
  color: #0082FF;
  width: 32px;
  height: 32px;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 1rem;
  font-weight: 600;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
  animation: quiz-completion-pulse 2s infinite;
}

@keyframes quiz-completion-pulse {
  0%, 100% { transform: scale(1); }
  50% { transform: scale(1.1); }
}

/* Quiz Game */
.quiz-game {
  background: var(--bg-secondary);
  border-radius: 20px;
  margin: 0 1rem 1.5rem;
  box-shadow: var(--shadow-lg);
  overflow: hidden;
  border: 1px solid var(--border-primary);
}

.quiz-game-header {
  background: linear-gradient(135deg, #0082FF, #1e40af);
  color: white;
  padding: 1.5rem;
  text-align: center;
}

.quiz-game-title {
  font-size: 1.5rem;
  font-weight: 600;
  margin-bottom: 0.5rem;
}

.quiz-progress-indicator {
  opacity: 0.9;
  font-size: 0.9rem;
  font-weight: 500;
}

/* Question Card */
.quiz-question-card {
  background: var(--bg-tertiary);
  border: 1px solid var(--border-primary);
  border-radius: 1rem;
  padding: 2rem;
  text-align: center;
  margin: 2rem 1.5rem;
  box-shadow: var(--shadow-sm);
}

.quiz-question-text {
  font-size: 1.25rem;
  font-weight: 600;
  color: var(--text-primary);
  margin: 0 0 2rem;
  line-height: 1.4;
}

/* Quiz Options */
.quiz-options {
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
}

/* True/False Layout */
.quiz-true-false {
  display: flex;
  gap: 1rem;
  justify-content: center;
  margin-top: 1.5rem;
}

/* Fill in the Blank */
.quiz-fill-blank {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 1rem;
  margin-top: 1.5rem;
}

.quiz-fill-input-container {
  display: flex;
  align-items: center;
  gap: 1rem;
}

.quiz-fill-input {
  width: 200px;
  padding: 1rem;
  border: 2px solid var(--border-primary);
  border-radius: 0.75rem;
  background: var(--bg-secondary);
  color: var(--text-primary);
  text-align: center;
  font-size: 1.1rem;
  transition: border-color 0.2s ease;
}

.quiz-fill-input:focus {
  outline: none;
  border-color: #0082FF;
  box-shadow: 0 0 0 3px rgba(0, 130, 255, 0.1);
}

/* Animations */
@keyframes quiz-spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

/* CSS Variables for Modern Theme Integration */
:root {
  --text-primary: #1c1917;
  --text-secondary: #78716c;
  --bg-primary: #f8fafc;
  --bg-secondary: #ffffff;
  --bg-tertiary: #f1f5f9;
  --border-primary: #e7e5e4;
  --border-secondary: #d6d3d1;
  --shadow-sm: 0 2px 8px rgba(0, 0, 0, 0.06);
  --shadow-md: 0 4px 16px rgba(0, 0, 0, 0.08);
  --shadow-lg: 0 12px 32px rgba(0, 0, 0, 0.12);
}

/* Dark mode variables */
.dark {
  --text-primary: #f8fafc;
  --text-secondary: #cbd5e1;
  --bg-primary: #0f172a;
  --bg-secondary: #1e293b;
  --bg-tertiary: #334155;
  --border-primary: #334155;
  --border-secondary: #475569;
  --shadow-sm: 0 2px 8px rgba(0, 0, 0, 0.3);
  --shadow-md: 0 4px 16px rgba(0, 0, 0, 0.4);
  --shadow-lg: 0 12px 32px rgba(0, 0, 0, 0.6);
}

/* Mobile Optimizations - IMPROVED */
@media (max-width: 768px) {
  .quiz-player-container {
    padding: 0;
  }
  
  .quiz-selector {
    padding: 2rem 1rem;
    margin-bottom: 1rem;
    border-radius: 0 0 20px 20px;
  }
  
  .quiz-progress-title {
    font-size: 1.75rem;
  }
  
  .quiz-progress-subtitle {
    font-size: 1rem;
  }
  
  .quiz-progress-stats {
    flex-direction: column;
    gap: 1rem;
    align-items: center;
  }
  
  .quiz-stat-pill {
    padding: 0.75rem 1.25rem;
    font-size: 0.85rem;
  }
  
  .quiz-grid {
    grid-template-columns: 1fr;
    gap: 1rem;
    padding: 0 0.5rem;
  }
  
  .quiz-card {
    min-height: 180px;
    padding: 1.25rem;
  }
  
  .quiz-card-icon {
    font-size: 2.5rem;
  }
  
  .quiz-card-title {
    font-size: 1rem;
  }
  
  .quiz-card-description {
    font-size: 0.8rem;
  }
  
  .quiz-card-stats {
    flex-direction: column;
    gap: 0.5rem;
  }
  
  .quiz-stat {
    justify-content: space-between;
  }
  
  .quiz-game {
    margin: 0 0.5rem 1rem;
  }
  
  .quiz-question-card {
    margin: 1.5rem 1rem;
    padding: 1.5rem 1rem;
  }
  
  .quiz-question-text {
    font-size: 1.1rem;
  }
  
  .quiz-true-false {
    flex-direction: column;
  }
  
  .quiz-fill-input {
    width: 150px;
    font-size: 1rem;
  }
  
  .quiz-back-section {
    padding: 0 1rem 1.5rem;
  }
}

@media (max-width: 480px) {
  .quiz-selector {
    padding: 1.5rem 1rem;
  }
  
  .quiz-progress-title {
    font-size: 1.5rem;
  }
  
  .quiz-grid {
    padding: 0 0.25rem;
  }
  
  .quiz-card {
    min-height: 160px;
    padding: 1rem;
  }
  
  .quiz-card-icon {
    font-size: 2rem;
  }
  
  .quiz-game-header {
    padding: 1rem;
  }
  
  .quiz-question-card {
    padding: 1rem 0.75rem;
  }
  
  .quiz-game-title {
    font-size: 1.25rem;
  }
  
  .quiz-question-text {
    font-size: 1rem;
  }
  
  .quiz-fill-input-container {
    flex-direction: column;
  }
  
  .quiz-fill-input {
    width: 100%;
    max-width: 200px;
  }
  
  .quiz-back-section {
    padding: 0 0.5rem 1.5rem;
  }
}