/* src/components/student/views/WordMatch/wordmatch.css */
/* 🎮 WORDMATCH STYLES - MODERN UI/UX TRANSFORMATION */

/* Container & Layout */
.wordmatch-player-container {
  max-width: 1000px;
  margin: 0 auto;
  padding: 0;
  min-height: 100vh;
}

.wordmatch-back-section {
  text-align: center;
  margin-top: 2rem;
  padding: 0 1rem 2rem;
}

/* Loading States */
.wordmatch-loading-container {
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
}

.wordmatch-loading-content {
  text-align: center;
}

.wordmatch-loading-spinner {
  width: 64px;
  height: 64px;
  border: 4px solid var(--border-primary);
  border-top: 4px solid #F59E0B;
  border-radius: 50%;
  animation: wordmatch-spin 1s linear infinite;
  margin: 0 auto 2rem;
  position: relative;
}

.wordmatch-loading-spinner::after {
  content: '';
  position: absolute;
  top: 8px;
  left: 8px;
  right: 8px;
  bottom: 8px;
  border: 2px solid transparent;
  border-top: 2px solid #ea580c;
  border-radius: 50%;
  animation: wordmatch-spin 1.5s linear infinite reverse;
}

.wordmatch-loading-text {
  font-size: 1.25rem;
  font-weight: 600;
  opacity: 0.9;
  color: var(--text-primary);
}

/* Word Match Selector - MODERN DESIGN */
.wordmatch-selector {
  background: var(--bg-secondary);
  border-radius: 0 0 24px 24px;
  padding: 2.5rem 2rem;
  margin-bottom: 2rem;
  box-shadow: var(--shadow-lg);
  border: 1px solid var(--border-primary);
  position: relative;
  overflow: hidden;
}

.wordmatch-selector::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 4px;
  background: linear-gradient(90deg, #F59E0B, #ea580c);
}

.wordmatch-progress-overview {
  text-align: center;
  margin-bottom: 2.5rem;
}

.wordmatch-progress-title {
  font-size: 2rem;
  font-weight: 700;
  color: var(--text-primary);
  margin-bottom: 0.75rem;
  letter-spacing: -0.02em;
  background: linear-gradient(135deg, #F59E0B, #ea580c);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

.wordmatch-progress-subtitle {
  color: var(--text-secondary);
  margin-bottom: 2rem;
  font-size: 1.1rem;
  font-weight: 400;
}

.wordmatch-progress-stats {
  display: flex;
  justify-content: center;
  gap: 2rem;
  margin-bottom: 2rem;
}

.wordmatch-stat-pill {
  background: var(--bg-tertiary);
  color: var(--text-primary);
  padding: 1rem 1.5rem;
  border-radius: 16px;
  font-size: 0.9rem;
  font-weight: 600;
  border: 1px solid var(--border-primary);
  box-shadow: var(--shadow-sm);
  transition: all 0.2s ease;
  backdrop-filter: blur(10px);
}

.wordmatch-stat-pill:hover {
  transform: translateY(-2px);
  box-shadow: var(--shadow-md);
}

.wordmatch-progress-bar-container {
  margin-bottom: 1.5rem;
  background: var(--bg-tertiary);
  border-radius: 12px;
  padding: 0.5rem;
  border: 1px solid var(--border-primary);
}

/* Rounds Grid - MODERN CARDS */
.wordmatch-rounds-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
  gap: 1.5rem;
  padding: 0 1rem;
}

.wordmatch-round-tab {
  background: var(--bg-tertiary);
  border: 2px solid var(--border-primary);
  border-radius: 20px;
  padding: 1.5rem;
  text-align: center;
  cursor: pointer;
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  position: relative;
  overflow: hidden;
  min-height: 180px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.wordmatch-round-tab::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 4px;
  background: linear-gradient(90deg, transparent, transparent);
  transition: all 0.3s ease;
}

.wordmatch-round-tab:hover {
  transform: translateY(-8px);
  box-shadow: 0 12px 40px rgba(0, 0, 0, 0.15);
  border-color: #F59E0B;
}

.wordmatch-round-tab:hover::before {
  background: linear-gradient(90deg, #F59E0B, #ea580c);
}

.wordmatch-round-tab.active {
  background: linear-gradient(135deg, #F59E0B, #ea580c);
  border-color: #F59E0B;
  color: white;
  transform: translateY(-4px) scale(1.02);
  box-shadow: 0 8px 32px rgba(245, 158, 11, 0.4);
}

.wordmatch-round-tab.active::before {
  background: linear-gradient(90deg, rgba(255, 255, 255, 0.3), rgba(255, 255, 255, 0.1));
}

.wordmatch-round-tab.completed {
  background: linear-gradient(135deg, #F59E0B, #ea580c);
  border-color: #F59E0B;
  color: white;
  box-shadow: 0 4px 20px rgba(245, 158, 11, 0.3);
}

.wordmatch-round-tab.completed::before {
  background: linear-gradient(90deg, rgba(255, 255, 255, 0.3), rgba(255, 255, 255, 0.1));
}

.wordmatch-round-icon {
  font-size: 3rem;
  display: block;
  margin-bottom: 1rem;
  transition: transform 0.3s ease;
}

.wordmatch-round-tab:hover .wordmatch-round-icon {
  transform: scale(1.1) rotate(5deg);
}

.wordmatch-round-title {
  font-size: 1.1rem;
  font-weight: 700;
  margin-bottom: 0.5rem;
  color: var(--text-primary);
  line-height: 1.3;
}

.wordmatch-round-tab.active .wordmatch-round-title,
.wordmatch-round-tab.completed .wordmatch-round-title {
  color: white;
}

.wordmatch-round-info {
  font-size: 0.85rem;
  opacity: 0.8;
  color: var(--text-secondary);
  margin-bottom: 1rem;
  font-weight: 500;
}

.wordmatch-round-tab.active .wordmatch-round-info,
.wordmatch-round-tab.completed .wordmatch-round-info {
  color: rgba(255, 255, 255, 0.9);
}

.wordmatch-completion-indicator {
  position: absolute;
  top: 1rem;
  right: 1rem;
  background: rgba(255, 255, 255, 0.9);
  color: #F59E0B;
  width: 32px;
  height: 32px;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 1rem;
  font-weight: 600;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
  animation: wordmatch-completion-pulse 2s infinite;
}

@keyframes wordmatch-completion-pulse {
  0%, 100% { transform: scale(1); }
  50% { transform: scale(1.1); }
}

/* Word Match Game */
.wordmatch-game {
  background: var(--bg-secondary);
  border-radius: 20px;
  margin: 0 1rem 1.5rem;
  box-shadow: var(--shadow-lg);
  overflow: hidden;
  border: 1px solid var(--border-primary);
}

.wordmatch-header {
  background: linear-gradient(135deg, #F59E0B, #ea580c);
  color: white;
  padding: 1.5rem;
  text-align: center;
}

.wordmatch-title {
  font-size: 1.5rem;
  font-weight: 600;
  margin-bottom: 0.5rem;
}

.wordmatch-subtitle {
  opacity: 0.9;
  margin-bottom: 1rem;
}

.wordmatch-progress-pills {
  display: flex;
  justify-content: center;
  gap: 1rem;
}

.wordmatch-progress-pill {
  background: rgba(255, 255, 255, 0.2);
  padding: 0.5rem 1rem;
  border-radius: 50px;
  font-size: 0.85rem;
  font-weight: 600;
}

/* Game Content */
.wordmatch-content {
  padding: 2rem 1.5rem;
}

.wordmatch-progress-section {
  margin-bottom: 2rem;
}

/* Words Grid */
.wordmatch-words-grid {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 2rem;
  margin-bottom: 2rem;
}

.wordmatch-words-column {
  background: var(--bg-tertiary);
  border: 1px solid var(--border-primary);
  border-radius: 16px;
  padding: 1rem;
  box-shadow: var(--shadow-sm);
}

.wordmatch-column-header {
  text-align: center;
  font-weight: 600;
  color: var(--text-primary);
  margin-bottom: 1rem;
  padding: 0.75rem;
  background: var(--bg-secondary);
  border: 1px solid var(--border-primary);
  border-radius: 12px;
  font-size: 0.9rem;
  box-shadow: var(--shadow-sm);
}

.wordmatch-word-list {
  display: flex;
  flex-direction: column;
  gap: 0.75rem;
}

.wordmatch-word-button {
  background: var(--bg-secondary);
  border: 2px solid var(--border-primary);
  border-radius: 12px;
  padding: 1rem;
  text-align: center;
  cursor: pointer;
  transition: all 0.2s ease;
  font-weight: 500;
  color: var(--text-primary);
  font-size: 0.95rem;
  box-shadow: var(--shadow-sm);
}

.wordmatch-word-button.selected {
  background: linear-gradient(135deg, #F59E0B, #ea580c);
  border-color: #F59E0B;
  color: white;
  transform: translateY(-2px) scale(1.02);
  box-shadow: 0 4px 16px rgba(245, 158, 11, 0.4);
}

.wordmatch-word-button:hover:not(:disabled):not(.selected) {
  border-color: #F59E0B;
  transform: translateY(-2px);
  box-shadow: var(--shadow-md);
  background: var(--bg-tertiary);
}

.wordmatch-word-button.selected:hover {
  background: linear-gradient(135deg, #F59E0B, #ea580c);
  border-color: #F59E0B;
  color: white;
  transform: translateY(-2px) scale(1.05);
  box-shadow: 0 6px 20px rgba(245, 158, 11, 0.5);
}

.wordmatch-word-button.matched {
  background: linear-gradient(135deg, #10b981, #059669);
  border-color: #10b981;
  color: white;
  cursor: default;
  opacity: 0.9;
  box-shadow: 0 2px 8px rgba(16, 185, 129, 0.3);
}

.wordmatch-word-button:disabled {
  cursor: not-allowed;
}

/* Instructions */
.wordmatch-instructions {
  background: var(--bg-tertiary);
  border: 1px solid var(--border-primary);
  border-radius: 12px;
  padding: 1rem;
  text-align: center;
  color: var(--text-secondary);
  font-size: 0.9rem;
  line-height: 1.5;
  box-shadow: var(--shadow-sm);
}

/* Perfect Score Animation */
.wordmatch-perfect-animation {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  font-size: 3rem;
  font-weight: 900;
  color: #fbbf24;
  text-shadow: 0 4px 8px rgba(0, 0, 0, 0.5);
  z-index: 1001;
  pointer-events: none;
  animation: wordmatch-perfect-pop 3s ease-out forwards;
}

/* Animations */
@keyframes wordmatch-spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

@keyframes wordmatch-perfect-pop {
  0% { transform: translate(-50%, -50%) scale(0) rotate(-12deg); opacity: 0; }
  20% { transform: translate(-50%, -50%) scale(1.4) rotate(-12deg); opacity: 1; }
  40% { transform: translate(-50%, -50%) scale(1) rotate(-12deg); opacity: 1; }
  70% { transform: translate(-50%, -50%) scale(1) rotate(-12deg); opacity: 1; }
  100% { transform: translate(-50%, -50%) scale(1) rotate(-12deg); opacity: 0; }
}

/* CSS Variables for Modern Theme Integration */
:root {
  --text-primary: #1c1917;
  --text-secondary: #78716c;
  --bg-primary: #f8fafc;
  --bg-secondary: #ffffff;
  --bg-tertiary: #f1f5f9;
  --border-primary: #e7e5e4;
  --border-secondary: #d6d3d1;
  --shadow-sm: 0 2px 8px rgba(0, 0, 0, 0.06);
  --shadow-md: 0 4px 16px rgba(0, 0, 0, 0.08);
  --shadow-lg: 0 12px 32px rgba(0, 0, 0, 0.12);
}

/* Dark mode variables */
.dark {
  --text-primary: #f8fafc;
  --text-secondary: #cbd5e1;
  --bg-primary: #0f172a;
  --bg-secondary: #1e293b;
  --bg-tertiary: #334155;
  --border-primary: #334155;
  --border-secondary: #475569;
  --shadow-sm: 0 2px 8px rgba(0, 0, 0, 0.3);
  --shadow-md: 0 4px 16px rgba(0, 0, 0, 0.4);
  --shadow-lg: 0 12px 32px rgba(0, 0, 0, 0.6);
}

/* Mobile Optimizations - IMPROVED */
@media (max-width: 768px) {
  .wordmatch-player-container {
    padding: 0;
  }
  
  .wordmatch-selector {
    padding: 2rem 1rem;
    margin-bottom: 1rem;
    border-radius: 0 0 20px 20px;
  }
  
  .wordmatch-progress-title {
    font-size: 1.75rem;
  }
  
  .wordmatch-progress-subtitle {
    font-size: 1rem;
  }
  
  .wordmatch-progress-stats {
    flex-direction: column;
    gap: 1rem;
    align-items: center;
  }
  
  .wordmatch-stat-pill {
    padding: 0.75rem 1.25rem;
    font-size: 0.85rem;
  }
  
  .wordmatch-rounds-grid {
    grid-template-columns: 1fr;
    gap: 1rem;
    padding: 0 0.5rem;
  }
  
  .wordmatch-round-tab {
    min-height: 160px;
    padding: 1.25rem;
  }
  
  .wordmatch-round-icon {
    font-size: 2.5rem;
  }
  
  .wordmatch-round-title {
    font-size: 1rem;
  }
  
  .wordmatch-round-info {
    font-size: 0.8rem;
  }
  
  .wordmatch-game {
    margin: 0 0.5rem 1rem;
  }
  
  .wordmatch-content {
    padding: 1.5rem 1rem;
  }
  
  .wordmatch-words-grid {
    gap: 1rem;
  }
  
  .wordmatch-word-button {
    padding: 0.75rem;
    font-size: 0.9rem;
  }
  
  .wordmatch-back-section {
    padding: 0 1rem 1.5rem;
  }
}

@media (max-width: 480px) {
  .wordmatch-selector {
    padding: 1.5rem 1rem;
  }
  
  .wordmatch-progress-title {
    font-size: 1.5rem;
  }
  
  .wordmatch-rounds-grid {
    padding: 0 0.25rem;
  }
  
  .wordmatch-round-tab {
    min-height: 140px;
    padding: 1rem;
  }
  
  .wordmatch-round-icon {
    font-size: 2rem;
  }
  
  .wordmatch-header {
    padding: 1rem;
  }
  
  .wordmatch-content {
    padding: 1rem 0.75rem;
  }
  
  .wordmatch-title {
    font-size: 1.25rem;
  }
  
  .wordmatch-words-grid {
    grid-template-columns: 1fr;
    gap: 1rem;
  }
  
  .wordmatch-words-column {
    padding: 0.75rem;
  }
  
  .wordmatch-word-button {
    padding: 0.6rem;
    font-size: 0.85rem;
  }
  
  .wordmatch-back-section {
    padding: 0 0.5rem 1.5rem;
  }
}