/* src/components/student/views/StudyCenter/study.css */
/* 🎮 GAMIFIED STUDY CENTER - Addictive Learning Experience */

/* ===================================================================== */
/* BASE CONTAINER WITH LV PATTERN */
/* ===================================================================== */

.study-center-container {
  min-height: 100vh;
  /* Louis Vuitton luxury pattern background */
  background: 
    repeating-linear-gradient(45deg, transparent, transparent 25px, rgba(255,255,255,0.02) 25px, rgba(255,255,255,0.02) 50px),
    repeating-linear-gradient(-45deg, transparent, transparent 25px, rgba(255,255,255,0.015) 25px, rgba(255,255,255,0.015) 50px),
    var(--bg-gradient);
  position: relative;
}

.study-loading-container {
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  background: 
    repeating-linear-gradient(45deg, transparent, transparent 25px, rgba(255,255,255,0.02) 25px, rgba(255,255,255,0.02) 50px),
    repeating-linear-gradient(-45deg, transparent, transparent 25px, rgba(255,255,255,0.015) 25px, rgba(255,255,255,0.015) 50px),
    var(--bg-gradient);
}

.study-loading-content {
  text-align: center;
  max-width: 280px;
  padding: 2rem;
  background: rgba(255, 255, 255, 0.05);
  backdrop-filter: blur(20px);
  border: 1px solid rgba(255, 255, 255, 0.1);
  border-radius: 20px;
}

/* ===================================================================== */
/* 🎯 SUPER COMPACT HEADER - Much smaller, gamified */
/* ===================================================================== */

.study-header {
  background: linear-gradient(135deg, #8b5cf6 0%, #7c3aed 50%, #6366f1 100%);
  border-radius: 0 0 20px 20px;
  position: relative;
  overflow: hidden;
  padding: 1rem 1rem 1.5rem;
  margin-bottom: 1rem;
  box-shadow: 0 4px 20px rgba(139, 92, 246, 0.4);
}

/* CSS Variables for Study Colors - Better dark mode support */
:root {
  --study-purple-light: #a855f7;
  --study-purple-medium: #8b5cf6;
  --study-purple-dark: #7c3aed;
  --study-purple-text: #8b5cf6;
  --study-purple-text-secondary: #7c3aed;
  --study-purple-emphasis: #8b5cf6;
  --study-purple-strong: #7c3aed;
}

.dark {
  --study-purple-light: #c084fc;
  --study-purple-medium: #a855f7;
  --study-purple-dark: #8b5cf6;
  --study-purple-text: #c084fc;
  --study-purple-text-secondary: #a855f7;
  --study-purple-emphasis: #f0abfc;
  --study-purple-strong: #d946ef;
}

/* Header luxury dot pattern */
.study-header::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: radial-gradient(circle at center, rgba(255, 255, 255, 0.15) 2px, transparent 2px);
  background-size: 25px 25px;
  opacity: 0.5;
  pointer-events: none;
}

/* Dark mode toggle removed - handled by main header */

.study-header-content {
  position: relative;
  max-width: 1200px;
  margin: 0 auto;
  z-index: 5;
}

/* Compact back button */
.study-back-button {
  display: inline-flex;
  align-items: center;
  gap: 0.375rem;
  color: rgba(255, 255, 255, 0.9);
  background: rgba(255, 255, 255, 0.1);
  backdrop-filter: blur(10px);
  border: 1px solid rgba(255, 255, 255, 0.2);
  border-radius: 10px;
  padding: 0.5rem 0.75rem;
  cursor: pointer;
  font-size: 0.8rem;
  font-weight: 500;
  transition: all 0.3s ease;
  margin-bottom: 1rem;
}

.study-back-button:hover {
  background: rgba(255, 255, 255, 0.2);
  transform: translateX(-2px);
}

/* Ultra compact header */
.study-header-title {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 1rem;
  margin-bottom: 1rem;
}

.study-icon-badge {
  width: 50px;
  height: 50px;
  background: rgba(255, 255, 255, 0.15);
  backdrop-filter: blur(20px);
  border: 2px solid rgba(255, 255, 255, 0.2);
  border-radius: 14px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 1.5rem;
  flex-shrink: 0;
}

.study-header-text {
  text-align: left;
}

.study-title {
  color: white;
  font-size: 1.4rem;
  font-weight: 700;
  margin: 0 0 0.25rem;
  letter-spacing: -0.02em;
}

.study-subtitle {
  color: rgba(255, 255, 255, 0.8);
  font-size: 0.8rem;
  margin: 0;
}

/* Gamified stats in single compact row */
.study-stats-pills {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 0.5rem;
}

.study-stat-pill {
  background: rgba(255, 255, 255, 0.1);
  backdrop-filter: blur(10px);
  border: 1px solid rgba(255, 255, 255, 0.2);
  border-radius: 10px;
  padding: 0.5rem 0.25rem;
  text-align: center;
  transition: all 0.3s ease;
  cursor: pointer;
}

.study-stat-pill:hover {
  background: rgba(255, 255, 255, 0.15);
  transform: translateY(-1px) scale(1.02);
}

.study-stat-icon {
  font-size: 1rem;
  margin-bottom: 0.125rem;
}

.study-stat-value {
  color: white;
  font-size: 0.875rem;
  font-weight: 600;
  line-height: 1;
  margin-bottom: 0.125rem;
}

.study-stat-label {
  color: rgba(255, 255, 255, 0.7);
  font-size: 0.6rem;
  font-weight: 500;
  text-transform: uppercase;
  letter-spacing: 0.05em;
}

/* ===================================================================== */
/* 🎮 GAMIFIED LAYOUT - RPG Style */
/* ===================================================================== */

.study-main-layout {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 1rem 2rem;
  display: grid;
  grid-template-columns: 300px 1fr;
  gap: 1rem;
  align-items: start;
}

/* ===================================================================== */
/* 🗡️ RPG SIDEBAR - Quest/Achievement Style */
/* ===================================================================== */

.study-sidebar {
  background: rgba(255, 255, 255, 0.05);
  backdrop-filter: blur(20px);
  border: 1px solid rgba(255, 255, 255, 0.1);
  border-radius: 16px;
  padding: 1.25rem;
  position: sticky;
  top: 1rem;
  max-height: calc(100vh - 2rem);
  overflow-y: auto;
}

.study-sidebar-header {
  text-align: center;
  margin-bottom: 1.25rem;
  padding-bottom: 1rem;
  border-bottom: 1px solid rgba(139, 92, 246, 0.2);
  position: relative;
}

.study-sidebar-header::after {
  content: '';
  position: absolute;
  bottom: -1px;
  left: 50%;
  transform: translateX(-50%);
  width: 30px;
  height: 2px;
  background: linear-gradient(90deg, #8b5cf6, #7c3aed);
  border-radius: 1px;
}

.study-sidebar-icon {
  font-size: 1.5rem;
  margin-bottom: 0.5rem;
}

.study-sidebar-title {
  font-size: 1rem;
  font-weight: 600;
  margin: 0 0 0.25rem;
  background: linear-gradient(135deg, var(--study-purple-text), var(--study-purple-text-secondary));
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

.study-sidebar-subtitle {
  font-size: 0.75rem;
  opacity: 0.7;
  margin: 0;
}

/* 🎯 Gamified guide list - Quest style */
.study-guides-list {
  margin-bottom: 1.25rem;
}

.study-guide-item {
  margin-bottom: 0.375rem;
  position: relative;
}

.study-guide-button {
  width: 100%;
  background: transparent;
  border: 2px solid rgba(139, 92, 246, 0.2);
  border-radius: 12px;
  padding: 0.875rem;
  cursor: pointer;
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  text-align: left;
  position: relative;
  overflow: hidden;
}

.study-guide-button::before {
  content: '';
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, transparent, rgba(139, 92, 246, 0.1), transparent);
  transition: left 0.6s ease;
}

.study-guide-button:hover::before {
  left: 100%;
}

.study-guide-button:hover:not(:disabled) {
  transform: translateY(-1px);
  box-shadow: 0 8px 25px rgba(139, 92, 246, 0.25);
  border-color: rgba(139, 92, 246, 0.4);
}

.study-guide-button.active {
  background: linear-gradient(135deg, #8b5cf6, #7c3aed);
  border-color: transparent;
  color: white;
  box-shadow: 0 8px 32px rgba(139, 92, 246, 0.4);
  transform: translateY(-2px);
}

.study-guide-button:disabled {
  opacity: 0.5;
  cursor: not-allowed;
}

/* Quest completion badge */
.study-guide-button.active::after {
  content: '✨';
  position: absolute;
  top: 0.5rem;
  right: 0.5rem;
  font-size: 0.8rem;
  animation: sparkle 2s ease-in-out infinite;
}

@keyframes sparkle {
  0%, 100% { opacity: 1; transform: scale(1); }
  50% { opacity: 0.5; transform: scale(1.2); }
}

.study-guide-header {
  display: flex;
  align-items: center;
  gap: 0.625rem;
  position: relative;
  z-index: 2;
}

.study-guide-icon {
  width: 32px;
  height: 32px;
  border-radius: 8px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 1rem;
  flex-shrink: 0;
  background: rgba(139, 92, 246, 0.1);
  color: var(--study-purple-text);
  transition: all 0.3s ease;
}

.study-guide-button.active .study-guide-icon {
  background: rgba(255, 255, 255, 0.2);
  color: white;
  transform: scale(1.1);
}

.study-guide-info {
  flex: 1;
  min-width: 0;
}

.study-guide-name {
  font-size: 0.85rem;
  font-weight: 600;
  margin-bottom: 0.125rem;
  display: flex;
  align-items: center;
  gap: 0.375rem;
  position: relative;
}

/* New badge for new content */
.study-guide-name .new-badge {
  background: linear-gradient(135deg, #f59e0b, #d97706);
  color: white;
  padding: 0.125rem 0.375rem;
  border-radius: 6px;
  font-size: 0.6rem;
  font-weight: 700;
  text-transform: uppercase;
  letter-spacing: 0.05em;
  animation: newPulse 2s ease-in-out infinite;
}

@keyframes newPulse {
  0%, 100% { transform: scale(1); }
  50% { transform: scale(1.05); }
}

.study-guide-status {
  font-size: 0.7rem;
  opacity: 0.7;
  display: flex;
  align-items: center;
  gap: 0.25rem;
}

.study-coming-soon {
  background: linear-gradient(135deg, #6b7280, #4b5563);
  color: white;
  padding: 0.125rem 0.375rem;
  border-radius: 6px;
  font-size: 0.6rem;
  font-weight: 600;
}

/* XP indicator */
.study-xp-indicator {
  font-size: 0.65rem;
  color: var(--study-purple-text);
  font-weight: 600;
  margin-left: auto;
}

/* Gamified progress bar */
.study-guide-progress {
  margin-top: 0.625rem;
  position: relative;
}

.study-progress-bar {
  width: 100%;
  height: 6px;
  background: rgba(139, 92, 246, 0.2);
  border-radius: 3px;
  overflow: hidden;
  position: relative;
}

.study-progress-fill {
  height: 100%;
  background: linear-gradient(90deg, #10b981, #059669);
  border-radius: 3px;
  transition: width 0.6s cubic-bezier(0.4, 0, 0.2, 1);
  position: relative;
}

.study-progress-fill::after {
  content: '';
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, transparent, rgba(255, 255, 255, 0.4), transparent);
  animation: progressShine 2s ease-in-out infinite;
}

@keyframes progressShine {
  0% { left: -100%; }
  50% { left: 100%; }
  100% { left: 100%; }
}

.study-progress-text {
  text-align: center;
  margin-top: 0.25rem;
  font-size: 0.65rem;
  opacity: 0.7;
  font-weight: 500;
}

/* Achievement unlock button */
.study-sidebar-actions {
  margin-bottom: 1.25rem;
  padding-top: 1rem;
  border-top: 1px solid rgba(139, 92, 246, 0.2);
}

.study-unlock-button {
  width: 100%;
  background: linear-gradient(135deg, #10b981, #059669);
  color: white;
  border: none;
  border-radius: 10px;
  padding: 0.75rem;
  font-size: 0.8rem;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 0.5rem;
  position: relative;
  overflow: hidden;
}

.study-unlock-button::before {
  content: '';
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, transparent, rgba(255, 255, 255, 0.2), transparent);
  transition: left 0.6s ease;
}

.study-unlock-button:hover::before {
  left: 100%;
}

.study-unlock-button:hover {
  transform: translateY(-1px);
  box-shadow: 0 6px 20px rgba(16, 185, 129, 0.4);
}

.study-complete-hint {
  font-size: 0.65rem;
  text-align: center;
  margin-top: 0.5rem;
  opacity: 0.6;
  line-height: 1.3;
}

/* Gamified tips box */
.study-sidebar-tips {
  background: linear-gradient(135deg, rgba(139, 92, 246, 0.1), rgba(99, 102, 241, 0.05));
  border-radius: 12px;
  padding: 1rem;
  border: 1px solid rgba(139, 92, 246, 0.2);
  position: relative;
  overflow: hidden;
}

.study-sidebar-tips::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 2px;
  background: linear-gradient(90deg, #8b5cf6, #6366f1, #8b5cf6);
  background-size: 200% 100%;
  animation: gradientShift 3s ease-in-out infinite;
}

@keyframes gradientShift {
  0%, 100% { background-position: 0% 50%; }
  50% { background-position: 100% 50%; }
}

.study-tip-icon {
  font-size: 1.25rem;
  text-align: center;
  margin-bottom: 0.5rem;
}

.study-tip-title {
  font-size: 0.8rem;
  font-weight: 600;
  margin: 0 0 0.5rem;
  text-align: center;
  background: linear-gradient(135deg, var(--study-purple-text), var(--study-purple-text-secondary));
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

.study-tip-text {
  font-size: 0.7rem;
  line-height: 1.4;
  margin: 0;
  text-align: center;
  opacity: 0.8;
}

/* ===================================================================== */
/* 📚 INTERACTIVE CONTENT AREA */
/* ===================================================================== */

.study-content-area {
  min-width: 0;
}

.study-content-card {
  background: rgba(255, 255, 255, 0.05);
  backdrop-filter: blur(20px);
  border: 1px solid rgba(255, 255, 255, 0.1);
  border-radius: 16px;
  overflow: hidden;
  transition: all 0.3s ease;
  position: relative;
}

.study-content-card::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 3px;
  background: linear-gradient(90deg, #8b5cf6, #7c3aed, #6366f1);
  background-size: 200% 100%;
  animation: gradientShift 4s ease-in-out infinite;
}

/* Compact section navigation */
.study-section-navigation {
  background: rgba(139, 92, 246, 0.05);
  border-bottom: 1px solid rgba(139, 92, 246, 0.15);
  padding: 1.25rem;
}

.study-section-nav-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 0.875rem;
}

.study-nav-title {
  font-size: 0.9rem;
  font-weight: 600;
  margin: 0;
  background: linear-gradient(135deg, var(--study-purple-text), var(--study-purple-text-secondary));
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

.study-section-counter {
  font-size: 0.75rem;
  opacity: 0.7;
  background: rgba(139, 92, 246, 0.1);
  padding: 0.25rem 0.5rem;
  border-radius: 8px;
  font-weight: 500;
}

.study-section-buttons {
  display: flex;
  gap: 0.375rem;
  flex-wrap: wrap;
}

.study-section-btn {
  background: transparent;
  border: 2px solid rgba(139, 92, 246, 0.3);
  border-radius: 8px;
  padding: 0.5rem 0.625rem;
  cursor: pointer;
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  font-size: 0.75rem;
  font-weight: 500;
  position: relative;
  overflow: hidden;
}

.study-section-btn::before {
  content: '';
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, transparent, rgba(139, 92, 246, 0.1), transparent);
  transition: left 0.5s ease;
}

.study-section-btn:hover::before {
  left: 100%;
}

.study-section-btn:hover {
  transform: translateY(-1px);
  box-shadow: 0 4px 12px rgba(139, 92, 246, 0.2);
}

.study-section-btn.active {
  background: linear-gradient(135deg, #8b5cf6, #7c3aed);
  border-color: transparent;
  color: white;
  transform: translateY(-1px);
  box-shadow: 0 4px 15px rgba(139, 92, 246, 0.3);
}

.study-section-btn-content {
  display: flex;
  align-items: center;
  gap: 0.375rem;
  position: relative;
  z-index: 2;
}

.study-section-number {
  width: 18px;
  height: 18px;
  border-radius: 4px;
  background: rgba(255, 255, 255, 0.2);
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 0.65rem;
  font-weight: 600;
}

/* Enhanced content body */
.study-content-body {
  padding: 1.75rem;
}

.study-formatted-content {
  font-size: 1rem;
  line-height: 1.6;
  color: var(--text-primary);
}

.study-formatted-content h2 {
  font-size: 1.4rem;
  font-weight: 700;
  margin: 1.75rem 0 1rem;
  background: linear-gradient(135deg, var(--study-purple-text), var(--study-purple-text-secondary));
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

.study-formatted-content h3 {
  font-size: 1.2rem;
  font-weight: 600;
  margin: 1.5rem 0 0.75rem;
  color: var(--study-purple-text);
}

.study-formatted-content p {
  margin: 1rem 0;
}

.study-formatted-content ul {
  margin: 1rem 0;
  padding-left: 1.5rem;
}

.study-formatted-content li {
  margin: 0.5rem 0;
}

.study-formatted-content blockquote {
  border-left: 3px solid var(--study-purple-text);
  background: linear-gradient(135deg, rgba(139, 92, 246, 0.05), rgba(99, 102, 241, 0.02));
  padding: 1rem 1.5rem;
  margin: 1.5rem 0;
  border-radius: 0 8px 8px 0;
  font-style: italic;
  position: relative;
}

.study-formatted-content blockquote::before {
  content: '💡';
  position: absolute;
  top: 1rem;
  right: 1rem;
  font-size: 1.2rem;
}

.study-formatted-content code {
  background: rgba(139, 92, 246, 0.1);
  color: var(--study-purple-text);
  padding: 0.125rem 0.375rem;
  border-radius: 4px;
  font-size: 0.9em;
  font-weight: 500;
  border: 1px solid rgba(139, 92, 246, 0.2);
}

.study-formatted-content strong {
  font-weight: 700;
  color: var(--study-purple-text);
}

.study-formatted-content em {
  font-style: italic;
  color: var(--study-purple-emphasis);
}

/* Gamified action section */
.study-action-section {
  padding: 1.5rem;
  text-align: center;
  border-top: 1px solid rgba(139, 92, 246, 0.15);
  background: linear-gradient(135deg, rgba(139, 92, 246, 0.02), rgba(99, 102, 241, 0.01));
}

.study-mark-read-section {
  margin-bottom: 1rem;
}

.study-mark-read-btn {
  background: linear-gradient(135deg, #8b5cf6, #7c3aed);
  color: white;
  border: none;
  border-radius: 12px;
  padding: 1rem 1.5rem;
  font-size: 0.9rem;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  display: inline-flex;
  align-items: center;
  gap: 0.5rem;
  position: relative;
  overflow: hidden;
}

.study-mark-read-btn::before {
  content: '';
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, transparent, rgba(255, 255, 255, 0.2), transparent);
  transition: left 0.6s ease;
}

.study-mark-read-btn:hover::before {
  left: 100%;
}

.study-mark-read-btn:hover {
  transform: translateY(-2px);
  box-shadow: 0 8px 25px rgba(139, 92, 246, 0.4);
}

.study-completed-badge {
  background: linear-gradient(135deg, #10b981, #059669);
  color: white;
  border-radius: 12px;
  padding: 1rem 1.5rem;
  display: inline-flex;
  align-items: center;
  gap: 0.5rem;
  font-size: 0.9rem;
  font-weight: 600;
  position: relative;
  animation: completionCelebration 1s ease-in-out;
}

@keyframes completionCelebration {
  0%, 100% { transform: scale(1); }
  50% { transform: scale(1.05); }
}

.study-reading-info {
  margin-top: 1rem;
}

.study-reading-time {
  display: flex;
  align-items: center;
  gap: 0.375rem;
  justify-content: center;
  font-size: 0.75rem;
  opacity: 0.7;
  background: rgba(139, 92, 246, 0.1);
  padding: 0.5rem 1rem;
  border-radius: 20px;
  display: inline-flex;
}

/* Compact bottom navigation */
.study-bottom-navigation {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1.25rem 1.75rem;
  border-top: 1px solid rgba(139, 92, 246, 0.15);
  background: linear-gradient(135deg, rgba(139, 92, 246, 0.02), rgba(99, 102, 241, 0.01));
}

.study-nav-progress {
  padding: 0.5rem 1rem;
  background: rgba(139, 92, 246, 0.1);
  border-radius: 12px;
  font-size: 0.75rem;
  font-weight: 500;
  border: 1px solid rgba(139, 92, 246, 0.2);
}

/* Navigation buttons */
.study-nav-btn {
  background: transparent;
  border: 2px solid rgba(139, 92, 246, 0.3);
  border-radius: 10px;
  padding: 0.625rem 1rem;
  cursor: pointer;
  transition: all 0.3s ease;
  font-size: 0.8rem;
  font-weight: 500;
  display: flex;
  align-items: center;
  gap: 0.375rem;
}

.study-nav-btn:hover:not(:disabled) {
  background: rgba(139, 92, 246, 0.1);
  border-color: rgba(139, 92, 246, 0.5);
  transform: translateY(-1px);
}

.study-nav-btn:disabled {
  opacity: 0.4;
  cursor: not-allowed;
}

/* ===================================================================== */
/* 🎉 XP CELEBRATION MODAL */
/* ===================================================================== */

.study-xp-celebration {
  text-align: center;
  padding: 1.5rem 1rem;
  background: linear-gradient(135deg, rgba(139, 92, 246, 0.1), rgba(99, 102, 241, 0.05));
  border-radius: 16px;
  position: relative;
  overflow: hidden;
}

.study-xp-celebration::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 3px;
  background: linear-gradient(90deg, #10b981, #8b5cf6, #10b981);
  background-size: 200% 100%;
  animation: gradientShift 2s ease-in-out infinite;
}

.study-xp-icon {
  font-size: 3.5rem;
  margin-bottom: 1rem;
  animation: celebrationBounce 1s ease-in-out;
}

.study-xp-title {
  font-size: 1.75rem;
  font-weight: 700;
  margin: 0 0 0.75rem;
  background: linear-gradient(135deg, #8b5cf6, #10b981);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

.study-xp-message {
  font-size: 1rem;
  margin: 0 0 1.5rem;
  opacity: 0.9;
}

.study-xp-details {
  background: rgba(255, 255, 255, 0.1);
  border-radius: 8px;
  padding: 1rem;
  margin-bottom: 1.5rem;
  text-align: left;
  font-size: 0.9rem;
  border: 1px solid rgba(255, 255, 255, 0.2);
}

/* ===================================================================== */
/* 📱 MOBILE RESPONSIVE - Ultra mobile optimized */
/* ===================================================================== */

@media (max-width: 768px) {
  .study-header {
    padding: 0.875rem 0.75rem 1.25rem;
    margin-bottom: 0.75rem;
  }
  
  .study-header-title {
    flex-direction: column;
    gap: 0.75rem;
  }
  
  .study-header-text {
    text-align: center;
  }
  
  .study-title {
    font-size: 1.25rem;
  }
  
  .study-icon-badge {
    width: 45px;
    height: 45px;
    font-size: 1.3rem;
  }
  
  .study-stats-pills {
    grid-template-columns: repeat(2, 1fr);
    gap: 0.375rem;
  }
  
  /* Mobile stack layout */
  .study-main-layout {
    grid-template-columns: 1fr;
    gap: 0.75rem;
    padding: 0 0.75rem 1.5rem;
  }
  
  .study-sidebar {
    order: 2;
    position: static;
    max-height: none;
    padding: 1rem;
  }
  
  .study-content-area {
    order: 1;
  }
  
  .study-content-body {
    padding: 1.25rem;
  }
  
  .study-section-navigation {
    padding: 1rem;
  }
  
  .study-section-buttons {
    gap: 0.25rem;
  }
  
  .study-section-btn {
    padding: 0.375rem 0.5rem;
    font-size: 0.7rem;
  }
  
  .study-action-section,
  .study-bottom-navigation {
    padding: 1rem 1.25rem;
  }
  
  .study-bottom-navigation {
    flex-direction: column;
    gap: 0.875rem;
  }
}

@media (max-width: 480px) {
  .study-stats-pills {
    grid-template-columns: 1fr;
  }
  
  .study-content-body {
    padding: 1rem;
  }
  
  .study-formatted-content {
    font-size: 0.95rem;
  }
  
  .study-section-buttons {
    justify-content: center;
  }
}

/* ===================================================================== */
/* 🎮 ANIMATIONS */
/* ===================================================================== */

@keyframes celebrationBounce {
  0%, 20%, 53%, 80%, 100% { transform: translate3d(0, 0, 0); }
  40%, 43% { transform: translate3d(0, -30px, 0); }
  70% { transform: translate3d(0, -15px, 0); }
  90% { transform: translate3d(0, -4px, 0); }
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

/* ===================================================================== */
/* 🎯 FOCUS STATES & ACCESSIBILITY */
/* ===================================================================== */

.study-back-button:focus,
.study-guide-button:focus,
.study-section-btn:focus,
.study-mark-read-btn:focus,
.study-nav-btn:focus {
  outline: 2px solid #8b5cf6;
  outline-offset: 2px;
}

/* Reduced motion */
@media (prefers-reduced-motion: reduce) {
  * {
    animation-duration: 0.01ms !important;
    animation-iteration-count: 1 !important;
    transition-duration: 0.01ms !important;
  }
}