/* src/components/student/views/StoryCompletion/story.css */
/* 📖 STORY COMPLETION STYLES - Extracted from 200+ lines of inline styles */

/* Container & Layout */
.story-player-container {
  max-width: 1000px;
  margin: 0 auto;
  padding: 0;
  min-height: 100vh;
}

.story-back-section {
  text-align: center;
  margin-top: 2rem;
  padding: 0 1rem 2rem;
}

/* Loading States */
.story-loading-container {
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
}

.story-loading-content {
  text-align: center;
}

.story-loading-spinner {
  width: 64px;
  height: 64px;
  border: 4px solid var(--border-primary);
  border-top: 4px solid #D32F2F;
  border-radius: 50%;
  animation: story-spin 1s linear infinite;
  margin: 0 auto 2rem;
  position: relative;
}

.story-loading-spinner::after {
  content: '';
  position: absolute;
  top: 8px;
  left: 8px;
  right: 8px;
  bottom: 8px;
  border: 2px solid transparent;
  border-top: 2px solid #0082FF;
  border-radius: 50%;
  animation: story-spin 1.5s linear infinite reverse;
}

.story-loading-text {
  font-size: 1.25rem;
  font-weight: 600;
  opacity: 0.9;
  color: var(--text-primary);
}

/* Story Selector - MODERN DESIGN */
.story-selector {
  background: var(--bg-secondary);
  border-radius: 0 0 24px 24px;
  padding: 2.5rem 2rem;
  margin-bottom: 2rem;
  box-shadow: var(--shadow-lg);
  border: 1px solid var(--border-primary);
  position: relative;
  overflow: hidden;
}

.story-selector::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 4px;
  background: linear-gradient(90deg, #D32F2F, #B71C1C);
}

.story-progress-overview {
  text-align: center;
  margin-bottom: 2.5rem;
}

.story-progress-title {
  font-size: 2rem;
  font-weight: 700;
  color: var(--text-primary);
  margin-bottom: 0.75rem;
  letter-spacing: -0.02em;
  background: linear-gradient(135deg, #D32F2F, #0082FF);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

.story-progress-subtitle {
  color: var(--text-secondary);
  margin-bottom: 2rem;
  font-size: 1.1rem;
  font-weight: 400;
}

.story-progress-stats {
  display: flex;
  justify-content: center;
  gap: 2rem;
  margin-bottom: 2rem;
}

.story-stat-pill {
  background: var(--bg-tertiary);
  color: var(--text-primary);
  padding: 1rem 1.5rem;
  border-radius: 16px;
  font-size: 0.9rem;
  font-weight: 600;
  border: 1px solid var(--border-primary);
  box-shadow: var(--shadow-sm);
  transition: all 0.2s ease;
  backdrop-filter: blur(10px);
}

.story-stat-pill:hover {
  transform: translateY(-2px);
  box-shadow: var(--shadow-md);
}

.story-progress-bar-container {
  margin-bottom: 1.5rem;
  background: var(--bg-tertiary);
  border-radius: 12px;
  padding: 0.5rem;
  border: 1px solid var(--border-primary);
}

/* Stories Grid - MODERN CARDS */
.story-stories-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
  gap: 1.5rem;
  padding: 0 1rem;
}

.story-tab {
  background: var(--bg-tertiary);
  border: 2px solid var(--border-primary);
  border-radius: 20px;
  padding: 1.5rem;
  text-align: center;
  cursor: pointer;
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  position: relative;
  overflow: hidden;
  min-height: 180px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.story-tab::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 4px;
  background: linear-gradient(90deg, transparent, transparent);
  transition: all 0.3s ease;
}

.story-tab:hover {
  transform: translateY(-8px);
  box-shadow: 0 12px 40px rgba(0, 0, 0, 0.15);
  border-color: #0082FF;
}

.story-tab:hover::before {
  background: linear-gradient(90deg, #0082FF, #1e40af);
}

.story-tab.active {
  background: linear-gradient(135deg, #D32F2F, #B71C1C);
  border-color: #D32F2F;
  color: white;
  transform: translateY(-4px) scale(1.02);
  box-shadow: 0 8px 32px rgba(211, 47, 47, 0.4);
}

.story-tab.active::before {
  background: linear-gradient(90deg, rgba(255, 255, 255, 0.3), rgba(255, 255, 255, 0.1));
}

.story-tab.completed {
  background: linear-gradient(135deg, #0082FF, #1e40af);
  border-color: #0082FF;
  color: white;
  box-shadow: 0 4px 20px rgba(0, 130, 255, 0.3);
}

.story-tab.completed::before {
  background: linear-gradient(90deg, rgba(255, 255, 255, 0.3), rgba(255, 255, 255, 0.1));
}

.story-icon {
  font-size: 3rem;
  display: block;
  margin-bottom: 1rem;
  transition: transform 0.3s ease;
}

.story-tab:hover .story-icon {
  transform: scale(1.1) rotate(5deg);
}

.story-tab-title {
  font-size: 1.1rem;
  font-weight: 700;
  margin-bottom: 0.5rem;
  color: var(--text-primary);
  line-height: 1.3;
}

.story-tab.active .story-tab-title,
.story-tab.completed .story-tab-title {
  color: white;
}

.story-tab-category {
  font-size: 0.85rem;
  opacity: 0.8;
  color: var(--text-secondary);
  margin-bottom: 1rem;
  font-weight: 500;
}

.story-tab.active .story-tab-category,
.story-tab.completed .story-tab-category {
  color: rgba(255, 255, 255, 0.9);
}

.story-completion-indicator {
  position: absolute;
  top: 1rem;
  right: 1rem;
  background: rgba(255, 255, 255, 0.9);
  color: #0082FF;
  width: 32px;
  height: 32px;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 1rem;
  font-weight: 600;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
  animation: story-completion-pulse 2s infinite;
}

@keyframes story-completion-pulse {
  0%, 100% { transform: scale(1); }
  50% { transform: scale(1.1); }
}

/* Story Game */
.story-game {
  background: var(--bg-secondary);
  border-radius: 20px;
  margin: 0 1rem 1.5rem;
  box-shadow: var(--shadow-lg);
  overflow: hidden;
  border: 1px solid var(--border-primary);
}

.story-header {
  background: linear-gradient(135deg, #D32F2F, #B71C1C);
  color: white;
  padding: 1.5rem;
  text-align: center;
}

.story-title {
  font-size: 1.5rem;
  font-weight: 600;
  margin-bottom: 0.5rem;
}

.story-subtitle {
  opacity: 0.9;
  margin-bottom: 1rem;
}

.story-progress-pills {
  display: flex;
  justify-content: center;
  gap: 1rem;
}

.story-progress-pill {
  background: rgba(255, 255, 255, 0.2);
  padding: 0.5rem 1rem;
  border-radius: 50px;
  font-size: 0.85rem;
  font-weight: 600;
}

/* Story Content */
.story-content {
  padding: 2rem 1.5rem;
}

.story-text {
  font-size: 1.125rem;
  line-height: 1.8;
  color: var(--text-primary);
  text-align: center;
  margin-bottom: 2rem;
  font-family: Georgia, serif;
}

.story-blank {
  display: inline-block;
  background: rgba(0, 130, 255, 0.1);
  border: 2px solid #0082FF;
  border-radius: 8px;
  padding: 0.25rem 0.75rem;
  margin: 0 0.25rem;
  color: #0082FF;
  font-weight: 600;
  min-width: 80px;
  text-align: center;
  cursor: pointer;
  transition: all 0.2s ease;
}

.story-blank.filled {
  background: rgba(16, 185, 129, 0.1);
  border-color: #10b981;
  color: #10b981;
}

.story-blank.active {
  background: rgba(245, 158, 11, 0.1);
  border-color: #F59E0B;
  color: #F59E0B;
  transform: scale(1.05);
}

/* Options Section */
.story-options-section {
  margin-bottom: 2rem;
}

.story-options-title {
  font-size: 1rem;
  font-weight: 600;
  color: var(--text-primary);
  text-align: center;
  margin-bottom: 1rem;
}

.story-options-grid {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 0.75rem;
}

@media (min-width: 640px) {
  .story-options-grid {
    grid-template-columns: repeat(4, 1fr);
  }
}

.story-option-button {
  background: var(--bg-tertiary);
  border: 2px solid var(--border-primary);
  border-radius: 12px;
  padding: 1rem 0.75rem;
  text-align: center;
  cursor: pointer;
  transition: all 0.2s ease;
  font-weight: 500;
  color: var(--text-primary);
  min-height: 64px;
  display: flex;
  align-items: center;
  justify-content: center;
  user-select: none;
}

.story-option-button:active {
  transform: scale(0.95);
}

.story-option-button.selected {
  background: linear-gradient(135deg, #0082FF, #1e40af);
  border-color: #0082FF;
  color: white;
  transform: scale(1.05);
}

/* Complete Section */
.story-complete-section {
  padding: 0 0 1rem;
  margin-top: 2rem;
}

/* Perfect Score Animation */
.story-perfect-animation {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  font-size: 3rem;
  font-weight: 900;
  color: #fbbf24;
  text-shadow: 0 4px 8px rgba(0, 0, 0, 0.5);
  z-index: 1001;
  pointer-events: none;
  animation: story-perfect-pop 3s ease-out forwards;
}

/* Animations */
@keyframes story-spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

@keyframes story-perfect-pop {
  0% { transform: translate(-50%, -50%) scale(0) rotate(-12deg); opacity: 0; }
  20% { transform: translate(-50%, -50%) scale(1.4) rotate(-12deg); opacity: 1; }
  40% { transform: translate(-50%, -50%) scale(1) rotate(-12deg); opacity: 1; }
  70% { transform: translate(-50%, -50%) scale(1) rotate(-12deg); opacity: 1; }
  100% { transform: translate(-50%, -50%) scale(1) rotate(-12deg); opacity: 0; }
}

/* CSS Variables for Modern Theme Integration */
:root {
  --text-primary: #1c1917;
  --text-secondary: #78716c;
  --bg-primary: #f8fafc;
  --bg-secondary: #ffffff;
  --bg-tertiary: #f1f5f9;
  --border-primary: #e7e5e4;
  --border-secondary: #d6d3d1;
  --shadow-sm: 0 2px 8px rgba(0, 0, 0, 0.06);
  --shadow-md: 0 4px 16px rgba(0, 0, 0, 0.08);
  --shadow-lg: 0 12px 32px rgba(0, 0, 0, 0.12);
}

/* Dark mode variables - Enhanced for modern feel */
.dark {
  --text-primary: #f8fafc;
  --text-secondary: #cbd5e1;
  --bg-primary: #0f172a;
  --bg-secondary: #1e293b;
  --bg-tertiary: #334155;
  --border-primary: #334155;
  --border-secondary: #475569;
  --shadow-sm: 0 2px 8px rgba(0, 0, 0, 0.3);
  --shadow-md: 0 4px 16px rgba(0, 0, 0, 0.4);
  --shadow-lg: 0 12px 32px rgba(0, 0, 0, 0.6);
}

/* Mobile Optimizations - IMPROVED */
@media (max-width: 768px) {
  .story-player-container {
    padding: 0;
  }
  
  .story-selector {
    padding: 2rem 1rem;
    margin-bottom: 1rem;
    border-radius: 0 0 20px 20px;
  }
  
  .story-progress-title {
    font-size: 1.75rem;
  }
  
  .story-progress-subtitle {
    font-size: 1rem;
  }
  
  .story-progress-stats {
    flex-direction: column;
    gap: 1rem;
    align-items: center;
  }
  
  .story-stat-pill {
    padding: 0.75rem 1.25rem;
    font-size: 0.85rem;
  }
  
  .story-stories-grid {
    grid-template-columns: 1fr;
    gap: 1rem;
    padding: 0 0.5rem;
  }
  
  .story-tab {
    min-height: 160px;
    padding: 1.25rem;
  }
  
  .story-icon {
    font-size: 2.5rem;
  }
  
  .story-tab-title {
    font-size: 1rem;
  }
  
  .story-tab-category {
    font-size: 0.8rem;
  }
  
  .story-game {
    margin: 0 0.5rem 1rem;
  }
  
  .story-content {
    padding: 1.5rem 1rem;
  }
  
  .story-text {
    font-size: 1rem;
    line-height: 1.6;
  }
  
  .story-blank {
    font-size: 0.9rem;
    padding: 0.2rem 0.5rem;
    min-width: 60px;
  }
  
  .story-options-grid {
    grid-template-columns: 1fr;
    gap: 0.5rem;
  }
  
  .story-option-button {
    padding: 0.75rem 0.5rem;
    min-height: 48px;
    font-size: 0.9rem;
  }
  
  .story-back-section {
    padding: 0 1rem 1.5rem;
  }
}

@media (max-width: 480px) {
  .story-selector {
    padding: 1.5rem 1rem;
  }
  
  .story-progress-title {
    font-size: 1.5rem;
  }
  
  .story-stories-grid {
    padding: 0 0.25rem;
  }
  
  .story-tab {
    min-height: 140px;
    padding: 1rem;
  }
  
  .story-icon {
    font-size: 2rem;
  }
  
  .story-header {
    padding: 1rem;
  }
  
  .story-content {
    padding: 1rem 0.75rem;
  }
  
  .story-title {
    font-size: 1.25rem;
  }
  
  .story-text {
    font-size: 0.95rem;
  }
  
  .story-back-section {
    padding: 0 0.5rem 1.5rem;
  }
}