/* src/components/student/views/HubView/hub.css */
/* 🎯 APPLE-STYLE HUBVIEW - Premium education platform design */

/* ===================================================================== */
/* CONTAINER & BASE LAYOUT */
/* ===================================================================== */

.hub-container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0;
}

/* ===================================================================== */
/* NEWS SECTION - Clean and minimal */
/* ===================================================================== */

.hub-news-section {
  margin-bottom: 2.5rem;
}

.hub-news-title {
  font-size: 1.5rem;
  font-weight: 600;
  margin-bottom: 1.5rem;
  text-align: center;
  letter-spacing: -0.01em;
  line-height: 1.2;
}

/* ===================================================================== */
/* PROGRESS CARD - Apple-style sophisticated design */
/* ===================================================================== */

.hub-progress-card {
  border-radius: 24px;
  padding: 0;
  margin-bottom: 3rem;
  position: relative;
  overflow: hidden;
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
}

.hub-progress-card:hover {
  transform: translateY(-2px);
}

/* Gradient accent bar at top */
.hub-progress-accent {
  height: 4px;
  width: 100%;
  border-radius: 24px 24px 0 0;
}

/* Progress header section */
.hub-progress-header {
  padding: 2.5rem 2.5rem 1.5rem;
  text-align: center;
}

.hub-progress-title {
  font-size: 2rem;
  font-weight: 700;
  margin: 0 0 0.75rem;
  letter-spacing: -0.02em;
  line-height: 1.1;
}

.hub-progress-subtitle {
  font-size: 1.125rem;
  margin: 0;
  font-weight: 400;
  opacity: 0.8;
}

/* Stats grid - Horizontal Apple-style layout */
.hub-progress-stats {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  gap: 1.5rem;
  padding: 0 2.5rem;
  margin-bottom: 2rem;
}

.hub-stat-card {
  text-align: center;
  border-radius: 16px;
  padding: 1.5rem 1rem;
  backdrop-filter: blur(10px);
  transition: all 0.2s ease;
  position: relative;
  overflow: hidden;
}

.hub-stat-card:hover {
  transform: translateY(-2px);
}

.hub-stat-card::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 2px;
  background: linear-gradient(90deg, transparent, rgba(255,255,255,0.2), transparent);
  opacity: 0;
  transition: opacity 0.3s ease;
}

.hub-stat-card:hover::before {
  opacity: 1;
}

.hub-stat-value {
  font-size: 2rem;
  font-weight: 600;
  margin-bottom: 0.5rem;
  letter-spacing: -0.02em;
  line-height: 1;
}

.hub-stat-highlight {
  background: linear-gradient(135deg, #0082FF, #0066CC);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

.hub-stat-label {
  font-size: 0.875rem;
  text-transform: uppercase;
  letter-spacing: 0.05em;
  font-weight: 500;
  opacity: 0.7;
}

/* Progress bar section */
.hub-progress-bar-section {
  padding: 0 2.5rem;
  margin-bottom: 2rem;
}

/* Achievements section */
.hub-achievements {
  display: flex;
  justify-content: center;
  gap: 3rem;
  padding: 1.5rem 2.5rem 2.5rem;
  border-top: 1px solid rgba(255, 255, 255, 0.1);
  margin-top: 1rem;
}

.hub-achievement {
  display: flex;
  align-items: center;
  gap: 0.75rem;
  padding: 1rem 1.5rem;
  border-radius: 16px;
  background: rgba(255, 255, 255, 0.05);
  backdrop-filter: blur(10px);
  transition: all 0.2s ease;
}

.hub-achievement:hover {
  background: rgba(255, 255, 255, 0.1);
  transform: translateY(-1px);
}

.hub-achievement-icon {
  width: 20px;
  height: 20px;
  flex-shrink: 0;
}

.hub-achievement-value {
  font-size: 1.25rem;
  font-weight: 600;
  letter-spacing: -0.01em;
}

.hub-achievement-label {
  font-size: 0.875rem;
  opacity: 0.8;
  font-weight: 400;
}

/* ===================================================================== */
/* ACTIVITIES SECTION - Premium card grid */
/* ===================================================================== */

.hub-activities-section {
  margin-top: 3rem;
}

.hub-activities-title {
  font-size: 1.75rem;
  font-weight: 700;
  margin-bottom: 2rem;
  text-align: center;
  letter-spacing: -0.02em;
  line-height: 1.1;
}

.hub-activities-grid {
  display: grid;
  grid-template-columns: 1fr;
  gap: 2rem;
}

/* Activity Cards - Apple-inspired design */
.hub-activity-card {
  border-radius: 24px;
  padding: 2.5rem;
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  cursor: pointer;
  position: relative;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  min-height: 320px;
}

.hub-activity-card:hover {
  transform: translateY(-6px);
}

.hub-activity-card.hub-activity-disabled {
  cursor: not-allowed;
  opacity: 0.8;
}

.hub-activity-card.hub-activity-disabled:hover {
  transform: translateY(-2px);
}

/* NEW badge for new activities */
.hub-new-badge {
  position: absolute;
  top: 1rem;
  right: 1rem;
  background: linear-gradient(135deg, #F59E0B, #D97706);
  color: white;
  font-size: 0.75rem;
  font-weight: 700;
  padding: 0.25rem 0.75rem;
  border-radius: 12px;
  text-transform: uppercase;
  letter-spacing: 0.025em;
  box-shadow: 0 2px 8px rgba(245, 158, 11, 0.3);
  animation: hub-pulse 2s infinite;
}

@keyframes hub-pulse {
  0%, 100% { 
    transform: scale(1); 
    opacity: 1; 
  }
  50% { 
    transform: scale(1.05); 
    opacity: 0.9; 
  }
}

/* Activity Icon - Larger, more prominent */
.hub-activity-icon {
  width: 80px;
  height: 80px;
  border-radius: 20px;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0 auto 2rem;
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  position: relative;
}

.hub-activity-icon::before {
  content: '';
  position: absolute;
  inset: -2px;
  border-radius: 22px;
  padding: 2px;
  background: linear-gradient(135deg, rgba(255,255,255,0.3), rgba(255,255,255,0.1));
  mask: linear-gradient(#fff 0 0) content-box, linear-gradient(#fff 0 0);
  mask-composite: subtract;
  opacity: 0;
  transition: opacity 0.3s ease;
}

.hub-activity-card:hover .hub-activity-icon {
  transform: scale(1.1) rotate(2deg);
}

.hub-activity-card:hover .hub-activity-icon::before {
  opacity: 1;
}

/* Activity Content */
.hub-activity-content {
  flex: 1;
  text-align: center;
  margin-bottom: 2rem;
}

.hub-activity-title {
  font-size: 1.5rem;
  font-weight: 700;
  text-align: center;
  margin-bottom: 1rem;
  letter-spacing: -0.01em;
  line-height: 1.2;
}

.hub-activity-description {
  font-size: 1rem;
  text-align: center;
  margin-bottom: 1.5rem;
  line-height: 1.5;
  opacity: 0.8;
}

.hub-activity-stats {
  text-align: center;
}

.hub-activity-detail {
  font-size: 0.9rem;
  margin-bottom: 0.75rem;
  opacity: 0.7;
}

.hub-activity-count {
  font-size: 1.125rem;
  font-weight: 600;
  margin-bottom: 0.5rem;
  letter-spacing: -0.01em;
}

/* Coming soon badge */
.hub-coming-soon-badge {
  color: white;
  font-size: 0.75rem;
  font-weight: 600;
  padding: 0.375rem 1rem;
  border-radius: 20px;
  display: inline-block;
  margin-top: 0.75rem;
  letter-spacing: 0.025em;
  text-transform: uppercase;
}

/* Activity features text */
.hub-activity-features {
  text-align: center;
  font-size: 0.8rem;
  margin-top: 1.5rem;
  line-height: 1.4;
  opacity: 0.6;
}

/* ===================================================================== */
/* RESPONSIVE DESIGN - Mobile-first Apple approach */
/* ===================================================================== */

/* Tablet Layout */
@media (min-width: 768px) {
  .hub-activities-grid {
    grid-template-columns: repeat(2, 1fr);
    gap: 2.5rem;
  }
  
  .hub-achievements {
    gap: 4rem;
  }
}

/* Desktop Layout */
@media (min-width: 1024px) {
  .hub-activities-grid {
    grid-template-columns: repeat(3, 1fr);
    gap: 3rem;
  }
  
  .hub-progress-stats {
    gap: 2rem;
  }
  
  .hub-achievements {
    gap: 5rem;
  }
}

/* Large Desktop */
@media (min-width: 1200px) {
  .hub-container {
    padding: 0 2rem;
  }
  
  .hub-progress-card {
    margin-bottom: 4rem;
  }
}

/* Mobile Optimizations */
@media (max-width: 767px) {
  .hub-progress-card {
    border-radius: 20px;
    margin-bottom: 2rem;
  }
  
  .hub-progress-header {
    padding: 2rem 1.5rem 1rem;
  }
  
  .hub-progress-title {
    font-size: 1.75rem;
  }
  
  .hub-progress-subtitle {
    font-size: 1rem;
  }
  
  .hub-progress-stats {
    gap: 1rem;
    padding: 0 1.5rem;
    margin-bottom: 1.5rem;
  }
  
  .hub-stat-card {
    padding: 1.25rem 0.75rem;
  }
  
  .hub-stat-value {
    font-size: 1.75rem;
  }
  
  .hub-stat-label {
    font-size: 0.8rem;
  }
  
  .hub-progress-bar-section {
    padding: 0 1.5rem;
  }
  
  .hub-achievements {
    gap: 2rem;
    padding: 1.5rem;
    flex-direction: column;
    align-items: center;
  }
  
  .hub-achievement {
    padding: 0.75rem 1.25rem;
  }
  
  .hub-activities-title {
    font-size: 1.5rem;
  }
  
  .hub-activities-grid {
    gap: 1.5rem;
  }
  
  .hub-activity-card {
    padding: 2rem 1.5rem;
    min-height: 280px;
  }
  
  .hub-activity-icon {
    width: 64px;
    height: 64px;
    border-radius: 16px;
    margin-bottom: 1.5rem;
  }
  
  .hub-activity-title {
    font-size: 1.25rem;
  }
  
  .hub-activity-description {
    font-size: 0.9rem;
  }
}

/* Small Mobile */
@media (max-width: 480px) {
  .hub-progress-header {
    padding: 1.5rem 1rem 1rem;
  }
  
  .hub-progress-stats {
    grid-template-columns: 1fr;
    gap: 1rem;
    padding: 0 1rem;
  }
  
  .hub-stat-card {
    padding: 1rem;
  }
  
  .hub-progress-bar-section {
    padding: 0 1rem;
  }
  
  .hub-achievements {
    padding: 1rem;
    gap: 1.5rem;
  }
  
  .hub-activity-card {
    padding: 1.5rem 1rem;
  }
  
  .hub-activity-icon {
    width: 56px;
    height: 56px;
    margin-bottom: 1.25rem;
  }
}

/* ===================================================================== */
/* ACCESSIBILITY & FOCUS STATES */
/* ===================================================================== */

.hub-activity-card:focus {
  outline: 2px solid var(--brand-blue, #0082FF);
  outline-offset: 4px;
}

.hub-stat-card:focus,
.hub-achievement:focus {
  outline: 2px solid var(--brand-blue, #0082FF);
  outline-offset: 2px;
}

/* Reduced motion preferences */
@media (prefers-reduced-motion: reduce) {
  .hub-progress-card,
  .hub-activity-card,
  .hub-stat-card,
  .hub-achievement,
  .hub-activity-icon {
    transition: none;
  }
  
  .hub-activity-card:hover .hub-activity-icon {
    transform: none;
  }
  
  .hub-progress-card:hover,
  .hub-activity-card:hover,
  .hub-stat-card:hover,
  .hub-achievement:hover {
    transform: none;
  }
  
  .hub-new-badge {
    animation: none;
  }
}