/* Teacher Alex Academy - Core Styles */
/* REMOVED CONFLICTING IMPORT: @import './styles/shared-theme.css'; */

body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

/* Reset any margins that might interfere */
html, body, #root {
  margin: 0;
  padding: 0;
  height: 100%;
}